const timer={
    data(){
        return {
            minYear:2005,
            maxYear:2035,
        }
    },
    computed:{
        yearRanges:function(){
            let years=[];
            for(let year=this.minYear%100;year<this.maxYear%100;year++){
                let previosYear=(year+1)<10?'0'+(year+1).toString():(year+1).toString();
                let currentYear=year<10?'0'+year.toString():year.toString();
                years.push(currentYear+previosYear);
            }
            return years;
        }
    },
    methods:{
        currentYear(){
            let date=new Date();
            let today=date.getFullYear()%100;
            if(date.getMonth()>3)
                today+=1;
            let previousYear=today-1;
            return previousYear.toString()+today.toString();
        },
        sanitizeString(year){
            return year.substring(0,2)+"-"+year.substring(2,4);
        }
    }
}

export default timer
