class ChalanDetailData{
    constructor(){
        this.chalanDetailData={
            id:null,
            chalanId:'',
            chalanInDate:null,
            chalanSlNo:0,
            platingId:null,
            itemCode:'',
            desc1:'',
            desc2:'',
            unit:'',
            qty:0,
            ordRate:0,
            outQty:0,
            cTag:0,
            returnTag:0,
            orderSlNo:0,
            orderID:null,
        }
        this.copyData={...this.chalanDetailData};
    }
    assignValues(data){
        this.chalanDetailData={...data};
        return this.chalanDetailData;
    }
    assignOrderValue(data){
        let chalQty=data.chalQty==null?0:data.chalQty;
        this.chalanDetailData.id=null;
        this.chalanDetailData.desc1=data.desc1;
        this.chalanDetailData.desc2=data.desc2;
        this.chalanDetailData.itemCode=data.itemCode;
        this.chalanDetailData.unit=data.unit;
        this.chalanDetailData.qty=data.qty-chalQty;
        this.chalanDetailData.platingId=data.platingID;
        this.chalanDetailData.orderSlNo=data.orderSerial;
        this.chalanDetailData.ordRate=data.orderRate;
        this.chalanDetailData.orderID=data.id;
        console.log(this.chalanDetailData);
        return this.chalanDetailData;
    }
    resetValue(){
        this.chalanDetailData={...this.copyData};
        return this.chalanDetailData;
    }
    validator(info){
        if(info.desc1.length==0)
            throw new Error('Description');
        if(info.platingId == null)
            throw new Error('Plating Type');
        if(info.unit.length==0)
            throw new Error('Unit Name');
    }
}

export default new ChalanDetailData();