<template>
    <div class='container'>
        <div v-if="lastFiveChalanIn.length>0" class="row top">
            <div class="col-md-12">
                <h4>Last 5 ChalanIn Entries</h4>
                <div v-for="chalan in lastFiveChalanIn" :key="chalan.id" class="card sized-card" @click="selectedChalanIn(chalan)" >
                    <div class="card-body blueViolet">
                        <h5 class="card-title">{{chalan.chalanId}}</h5>
                        <p class="card-text">Date: {{properDate(chalan.chalanDate)}}</p>
                        <p style="font-size:small;" class="card-text text-truncate">{{partyName(chalan.partyID)}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="lastFiveChalanOut.length>0" class="row top">
            <h4>Last 5 ChalanOut Entries</h4>
            <div class="col-md-12">
                 <div v-for="chalan in lastFiveChalanOut" :key="chalan.id" @click="selectedChalanOut(chalan)" class="card sized-card">
                    <div class="card-body corn">
                        <h5 class="card-title">{{chalan.chalanOutID}}</h5>
                        <p class="card-text">Date: {{properDate(chalan.outDate)}}</p>
                        <p style="font-size:small;" class="card-text text-truncate">{{partyName(chalan.partyID)}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="lastFiveBill.length>0" class="row top">
            <h4>Last 5 Bill Entries</h4>
            <div class="col-md-12">
                 <div v-for="chalan in lastFiveBill" :key="chalan.id" @click="selectedBill(chalan)" class="card sized-card">
                    <div class="card-body olive">
                        <h5 class="card-title">{{chalan.billNo}}</h5>
                        <p class="card-text">Date: {{properDate(chalan.billDate)}}</p>
                        <p style="font-size:small;" class="card-text text-truncate">{{partyName(chalan.partyID)}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default{
    name:'MainMenuButtons',
    computed:{
        PartyMaster:function(){
            return this.$store.state.partyMaster.map(data=>data.id);
        },
        lastFiveChalanIn:function(){
            let master=this.$store.state.financialYearData['chalan_in_masters'];
            if(master == null)
                return [];
            return master.slice(Math.max(master.length-5,0));
        },
        lastFiveChalanOut:function(){
            let master=this.$store.state.financialYearData['chalan_out_masters'];
            if(master == null)
                return [];
            return master.slice(Math.max(master.length-5,0));
        },
        lastFiveBill:function(){
            let master=this.$store.state.financialYearData['bill_masters'];
            if(master == null)
                return [];
            return master.slice(Math.max(master.length-5,0));
        }
    },
    methods:{
        selectedChalanIn(chalan){
            this.$router.push({name:"ChallanIn",params:{initialData:chalan}});
        },
        selectedChalanOut(chalan){
            this.$router.push({name:"ChallanOut",params:{initialData:chalan}});
        },
        selectedBill(chalan){
            this.$router.push({name:"BillEntry",params:{initialData:chalan}});
        },
        properDate(date){
            return new Date(date).toLocaleDateString('en-GB');
        },
        partyName(id){
            let pName=this.$store.state.partyMaster[this.PartyMaster.indexOf(id)];
            if(pName != null)
                return pName.partyName;
        }
    }
}
</script>
<style scoped>
@media (max-width: 767px) {
    .sized-card{
        width: 50%;
    }
}
@media (min-width: 768px) {
    .sized-card{
        width: 20%;
    }
}
    .top{
        margin-top: 3%;
    }
    .blueViolet{
        background-color: blueviolet;
        height: 25%;
    }
    .corn{
        background-color: cornflowerblue;
        height: 25%;
    }
    .olive{
        background-color: olive;
        height: 25%;
    }
</style>