<template>
    <div class="container">
        <div class="form-group row no-gutters" style="margin-top:10px;">
            <label for="PartyName" class="col-md-1 col-form-label text-center">Party Name:</label>
            <partydropdown :PartyMaster="PartyMaster" :partyID.sync="partyID"></partydropdown>

            <label for="orderText" class="col-md-1 col-form-label text-center">Order No.:</label>
            <div class="col-md-2">
                <input type="text" id="orderText" class="form-control" v-model="orderSerial">                
            </div>

            <div class="col-md">
                <button type="button" class="btn btn-info" data-toggle="modal" data-target="#myModal">&#8595;</button>
            </div>

            <label for="datePicker" class="col-md-1 col-form-label text-center">Date:</label>
            <div class="col-md-2">
                <input id="datePicker" type="date" class="form-control" v-model="orderDate">
            </div>
        </div>

        <hr>
        <ordermaster :orderDetails="orderDetails" :platingDetails="Platings" @find="findBySerial" @delete="deleteValue"></ordermaster>
        <hr>

        <ordermodal id="myModal" :partyorders="partyOrders" @selectedorder="selectedOrder"></ordermodal>

        <div class="row form-group no-gutters">
            <label for="platingType" class="col-md-2 col-form-label text-center">Plating Type:</label>
            <platingtype :platingID.sync="orderDetail.platingID" :Platings="Platings"></platingtype>
            
            <label for="itemCode" class="col-md-2 col-form-label text-center">Item Code:</label>
            <div class="col-md-2">
                <input type="text" id="itemCode" class="form-control" v-model="orderDetail.itemCode">
            </div>
        </div>

        <div class="row form-group no-gutters">
            <label for="itemDescription" class="col-md-2 col-form-label text-center">Item Description:</label>
            <div class="col-md-10">
                <input type="text" id="itemDescription" class="form-control" v-model="orderDetail.desc1">
            </div>
        </div>

        <div class="form-group row no-gutters">  
            <span class="col-md-2"></span>              
            <div class="col-md-10">
                <input type="text" id="itemDescription2" class="form-control" v-model="orderDetail.desc2">
            </div>
        </div>

        <div class="row form-group no-gutters">
            <label for="qtyType" class="col-md-1 col-form-label text-center">Qty:</label>
            <div class="col-md-1">
                <input type="number" id="qtyType" class="form-control" v-model="orderDetail.qty">
            </div>
            
            <label for="unitCode" class="col-md-1 col-form-label text-center">Unit:</label>
            <unitdropdown :unitType.sync="orderDetail.unit" :Units="Units"></unitdropdown>
            
            <label for="rateCode" class="col-md-1 col-form-label text-center">Rate:</label>
            <div class="col-md-1">
                <input type="number" id="rateCode" class="form-control" v-model="orderDetail.orderRate">
            </div>

            <div class="col-md-4 text-center">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="addDetail()">Add</button>
                    <button class="mx-2 btn btn-danger" @click="reset()">Clear</button>
                </div>
            </div>            
        </div>

        <div class="row">
            <div class="col-md-12 text-center">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="newButton">New</button>
                    <button class="mx-2 btn btn-danger" :disabled="!editing" @click="updateButtonDialog">Update</button>
                    <button class="mx-2 btn btn-danger" @click="deleteButton">Delete</button>
                    <button class="mx-2 btn btn-danger" @click="navigateTo('/')" >Back</button>
                </div>
            </div>
        </div>

        <alertmodal id='alertmodal'  @updateVal="updateButton" @deleteVal="deleteOrderMaster" ref="alertmodal"></alertmodal>
    </div>
</template>
<script>
import navigation from '@/mixins/navigation.js'
import deleteCheck from '@/mixins/deleteCheck.js'
import ordermaster from './OrderMasterTable'
import ordermodal from './OrderMasterModal'
import partydropdown from '../CommonComponents/DropDownParty'
import platingtype from '../CommonComponents/DropDownPlating'
import unitdropdown from '../CommonComponents/DropDownUnit'
import OrderDetails from '@/model/OrderMaster.js'
import alertmodal from '../CommonComponents/Alerts';
import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'

export default {
    name:'OrderMaster',
    components:{
        ordermaster,ordermodal,partydropdown,platingtype,unitdropdown,alertmodal
    },
    data(){
        return{
            orderDetail:OrderDetails.orderDetail,
            orderDetails:[],
            //
            orderSerial:'',
            orderID:null,
            orderDate:null,
            //
            editing:false,
            partyID:null,
            partyOrders:[],
            deleteOrders:[],
        }
    },
    mixins:[navigation,deleteCheck],
    computed:{
        alertDialogue:function(){
            return this.$refs.alertmodal;
        },
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        },
        Units:function(){
            return this.$store.state.units;
        },
        Platings:function(){
            if(this.$store.state.companyName == 0)
                return this.$store.state.platingID["RECORD"];
            else
                return this.$store.state.platingID["PLATINGMST"];
        },
        OrderMasterURLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    master:URLBuilder.buildURL(URLBuilder.endpoints.ORDERMASTER),
                    details:URLBuilder.buildURL(URLBuilder.endpoints.ORDERDETAILS),
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpoints.CHALANINMASTER),
                };
            else
                return {
                    master:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERMASTER),
                    details:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERDETAILS),
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANINMASTER),
                };
        },
    },
    methods:{
        addDetail(){
            try{
                OrderDetails.validator(this.orderDetail);
            }catch(error){
                this.alertDialogue.error(error,2);
                return;
            }
            if(this.orderDetail.orderSerial==0){
                this.orderDetail.orderSerial=this.orderDetails.length+1;
                this.orderDetails.push({...this.orderDetail});
            }else{
                let index=this.orderDetail.orderSerial-1;
                this.orderDetails.splice(index,1,{...this.orderDetail});
            }
            this.reset();
        },
        validator(){
            if(this.orderSerial.length == 0)
                throw new Error('Order Number');
            if(this.orderDate == null)
                throw new Error('Order Date');
            if(this.partyID == null)
                throw new Error('Party Name');
        },
        reset(){
            this.orderDetail=OrderDetails.reset();
        },
        resetAll(){
            this.orderSerial=null;
            this.orderID=null;
            this.orderDate="";
            this.reset();
            this.orderDetails=[];
            this.deleteOrders=[];
            this.editing=false;
        },
        findBySerial(id){
            this.orderDetail=OrderDetails.assignValues(this.orderDetails[--id]);
        },
        deleteValue(id){
            id=id-1;
            if(this.orderDetails[id].id != null)
                this.deleteOrders.push(this.orderDetails[id].id);

            this.orderDetails.splice(id,1);
            for(let i=0;i<this.orderDetails.length;i++){
                this.orderDetails[i].orderSerial=i+1;
            }
        },
        selectedOrder(order){
            this.orderSerial=order.orderNumber;
            this.orderID=order.id;
            this.orderDate=order.orderDate;
            this.fetchOrderDetails(this.orderSerial);
            this.reset();
            this.editing=true;
        },
        newButton(){
            if(this.orderID != null)
                this.resetAll();
            else
                this.createNewOrder();
        },
        updateButtonDialog(){
            this.alertDialogue.update(this.orderSerial);
        },
        updateButton(){
            if(this.deleteOrders.length>0){
                this.deleteOrders.forEach((order)=>{
                    this.deleteOrderDetails(order);
                    console.log(order);
                });
            }
            this.updateOrderMaster();
            this.orderDetailsCreatorUpdater();
        },
        async deleteButton(){
            if(this.orderID != null){
                try{
                    await this.orderMasterDeleteCheck(this.OrderMasterURLs.chalanInMaster,this.orderSerial);
                }
                catch(error){
                    this.alertDialogue.error(error,1);
                    return;
                }
                this.alertDialogue.delete(this.orderSerial);
            }
            else
                this.resetAll();
        },
        orderDetailsCreatorUpdater(){
            let promiseArray=[];
            this.orderDetails.forEach(order => {
                if(order.id != null){
                    promiseArray.push(this.updateOrderDetails(order));
                }else{
                    promiseArray.push(this.createNewOrderDetails(order));
                }
            });
            Promise.all(promiseArray).then(()=>{
                if(this.orderID==null)
                    this.alertDialogue.success(this.orderSerial,1);
                else
                    this.alertDialogue.success(this.orderSerial,2);
                this.resetAll();
            }).catch(error=>{
                this.alertDialogue.error(error,3);
                return;
            });            
        },
        orderMasterPayload(){
            return {orderNumber:this.orderSerial,partyID:this.partyID,orderDate:this.orderDate};
        },
        orderDetailsPayload(order){
            order['orderNumber']=this.orderSerial;
            order['orderDate']=this.orderDate;
            return order;
        },
        fetchPartyOrders(){
            ApiCalls.search(this.OrderMasterURLs.master,"partyID",this.partyID).then((res)=>{
                this.partyOrders=res.data;
            });
        },
        fetchOrderDetails(orderSerial){
            this.orderDetails=[];
            ApiCalls.search(this.OrderMasterURLs.details,"orderNumber",orderSerial).then((res)=>{                
                for(let x=0;x<res.data.length;x++){
                    this.orderDetail=OrderDetails.assignValues(res.data[x]);
                    this.addDetail();
                }
            });
        },
        createNewOrder(){
            try{
                this.validator();
            }catch(error){
                this.alertDialogue.error(error,2);
                return;
            }
            ApiCalls.post(this.OrderMasterURLs.master,this.orderMasterPayload()).then((res)=>{
                console.log(res.data);
                this.orderDetailsCreatorUpdater();
            }).catch(error=>{
                this.alertDialogue.error(error,3);
                return;
            })
            this.fetchPartyOrders();
        },
        updateOrderMaster(){
            ApiCalls.update(this.OrderMasterURLs.master+"/"+this.orderID,this.orderMasterPayload()).then((res)=>{
               console.log(res.data);
           });
        },
        async createNewOrderDetails(order){            
           return await ApiCalls.post(this.OrderMasterURLs.details,this.orderDetailsPayload(order)).then((res)=>{
               console.log(res.data);
           });
        },
        async updateOrderDetails(order){
           return await ApiCalls.update(this.OrderMasterURLs.details+"/"+order.id,this.orderDetailsPayload(order)).then((res)=>{
               console.log(res.data);
           });
        },
        deleteOrderMaster(){
            ApiCalls.delete(this.OrderMasterURLs.master+"/"+this.orderID).then((res)=>{
                console.log(res.data);
                let promises=[];
                this.orderDetails.forEach(order => {                    
                    if(order.id != null){
                        promises.push(this.deleteOrderDetails(order.id));
                    }
                });
                if(this.deleteOrders.length>0){
                    this.deleteOrders.forEach((order)=>{
                        promises.push(this.deleteOrderDetails(order));
                    });
                }   
                Promise.all(promises).then(()=>{
                    this.alertDialogue.success(this.orderSerial,3);
                    this.resetAll();
                    this.fetchPartyOrders();
                }).catch(error=>{
                    this.alertDialogue.error(error,3);
                })                
            });
        },
        async deleteOrderDetails(orderID){
            await ApiCalls.delete(this.OrderMasterURLs.details+"/"+orderID).then((res)=>{
                console.log(res.data);
            });
        }
    },
    watch:{
        partyID:function(){
            this.fetchPartyOrders();
        }
    }
}
</script>

