class BillDetailData{
    constructor(){
        this.billDetailData={
            id:null,
            billNumber:'',
            billSlNo:0,
            billDate:null,
            chalanOutId:null,
            chalanOutSlNo:0,
            chalanOutDate:null,//
            desc1:'',
            orderNumber:null,
            orderDate:null,
            qty:0,
            unit:'',
            rate:0,
            amount:0,
            finYR:0,
            billTag:0,
            chalanOutDetail:{
                platingType:'',
            },
            chalanInDetail:{
                itemCode:'',
                desc2:'',
            },
        }
        this.copyData={...this.billDetailData};
    }
    assignValues(data){
        this.billDetailData={...data};
        return this.billDetailData;
    }
    assignValuesFromChalanOut(chalan){
        let billQty=chalan.billQty==null?0:chalan.billQty;
        this.billDetailData.id=null;
        this.billDetailData.chalanOutDetail=chalan.chalanOutData;
        this.billDetailData.chalanInDetail=chalan.chalanInData;
        this.billDetailData.chalanOutId=chalan.chalanOutData.chalanOutID;
        this.billDetailData.chalanOutSlNo=chalan.chalanOutData.chalanOutSl;
        this.billDetailData.chalanOutDate=chalan.chalanOutData.chalanOutDate;
        this.billDetailData.desc1=chalan.chalanOutData.desc1;
        this.billDetailData.orderNumber=chalan.chalanOutData.orderNo;
        this.billDetailData.qty=chalan.chalanOutData.qty-billQty;
        this.billDetailData.unit=chalan.chalanOutData.unit;
        this.billDetailData.rate=chalan.chalanOutData.rate;
        this.billDetailData.amount=chalan.chalanOutData.amount;
        this.billDetailData.finYR=chalan.chalanOutData.finYR;
        return this.billDetailData;
    }
    calculateAmount(){
        this.billDetailData.amount=this.billDetailData.rate*this.billDetailData.qty;
    }
    resetValue(){
        this.billDetailData={...this.copyData};
        return this.billDetailData;
    }
    validator(info){
        if(info.billNo.length==0)
            throw new Error('Description');
        if(info.chalanOutId == null)
            throw new Error('Chalan OutID');
    }
}

export default new BillDetailData();