class URLBuilder{
    constructor(){
        this.url="https://gdelectroplating.in/";
        this.endpoints={
            PARTYMASTER:"party-masters",
            ORDERMASTER:"order-masters",
            ORDERDETAILS:"order-details",
            FINANCIALYEAR:"financial-years",
            CHALANINMASTER:"chalan-in-masters",
            CHALANINDETAILS:"chalan-in-details",
            CHALANOUTMASTER:"chalan-out-masters",
            CHALANOUTDETAILS:"chalan-out-details",
            BILLMASTER:"bill-masters",
            BILLDETAILS:"bill-details"
        }
        this.endpointsFA={
            PARTYMASTER:"party-masters-fa",
            ORDERMASTER:"order-masters-fa",
            ORDERDETAILS:"order-details-fa",
            FINANCIALYEAR:"financial-years-fa",
            CHALANINMASTER:"chalan-in-masters-fa",
            CHALANINDETAILS:"chalan-in-details-fa",
            CHALANOUTMASTER:"chalan-out-masters-fa",
            CHALANOUTDETAILS:"chalan-out-details-fa",
            BILLMASTER:"bill-masters-fa",
            BILLDETAILS:"bill-details-fa"
        }
        this.units="uploads/bd3e38352f87481da1b7f7b520799b65.json";
        this.platingID="uploads/7f3ebc8b95b24d54bd1bb4751006ebc1.json";
    }
    buildURL(endpoint,limit=""){
        let url=this.url+endpoint+limit;
        return url;
    }
    getUnitURL(){
        return this.url+this.units;
    }
    getPlatingURL(){
        return this.url+this.platingID;
    }
}

export default new URLBuilder()