<template>
    <nav class="navbar navbar-expand-lg navbar-custom navbar-dark">
    <!-- Brand -->
    <!-- <a class="navbar-brand" href="#">Logo</a> -->
    <router-link to="/">
        <a class="navbar-brand" @click="changeCompany" id="companyName">G.D. Electroplating Works</a>
    </router-link>
    <!-- Links -->
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav ml-auto">
            
            <router-link to="/partyMaster" tag="li" active-class="active">
                <a class="nav-link">Party Master</a>
            </router-link>

            <router-link to="/orderMaster" tag="li" active-class="active">
                <a class="nav-link">Order Master</a>
            </router-link>

            <router-link to="/challanIn" tag="li" active-class="active">
                <a class="nav-link">Challan In</a>
            </router-link>

            <router-link to="/challanOut" tag="li" active-class="active">
                <a class="nav-link">Challan Out</a>
            </router-link>

            <router-link to="/billEntry" tag="li" active-class="active">
                <a class="nav-link">Billing</a>
            </router-link>
            
            <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" id="navbardrop" data-toggle="dropdown">
                Rejection
            </a>
            <div class="dropdown-menu">
                <router-link to="/rejAdjustment" tag='a' class="dropdown-item" active-class="active">Rejection Adjustment</router-link>
                <router-link to="/rejChalanIn" tag='a' class="dropdown-item" active-class="active">Rejection In</router-link>
            </div>
            </li>
            
            <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" id="navbardrop" data-toggle="dropdown">
                Reports
            </a>
            <div class="dropdown-menu dropdown-menu-right">
                <a class="dropdown-item" data-toggle="modal" data-target="#materialModal">Material Movement Report</a>
                <a class="dropdown-item" data-toggle="modal" data-target="#contractorModal">Contractors Bill</a>
                <a class="dropdown-item" data-toggle="modal" data-target="#orderbills">Bills Against Order</a>
            </div>
            </li>
            <li style="width:100px;float:right;">
                <div >
                <select id="typeText" class="form-control" v-model="finYR">
                    <option  v-for="year in yearRanges" :key="year" :value="year">
                        {{sanitizeString(year)}}
                    </option>
                </select>
            </div>
            </li>  

        </ul>
    </div>
         <materialmodal @print='printData' id="materialModal"></materialmodal> 
         <contractormodal @print='printData' id="contractorModal"></contractormodal>   
         <orderbills  @print='printData' id="orderbills"></orderbills>  
    </nav>
</template>

<script>
import navigation from '@/mixins/navigation.js'
import timer from '@/mixins/timer.js'
import update from '@/mixins/update.js'
import materialmodal from './MaterialMovementModal';
import contractormodal from './GovernmentContractorModal';
import socket from '@/socketevent.js'
import orderbills from './OrderBillsChecker';
//const ipc=window.require('electron').ipcRenderer
export default {
    name:"TopNavBar",
    data(){
        return{
            finYR:this.currentYear(),
        }
    },
    components:{
        materialmodal,contractormodal,orderbills
    },
    mixins:[navigation,timer,update],
    mounted(){
        let SocketEvent=new socket();
        let c=window.localStorage.getItem('Company');
        if(c==1)
            this.changeCompany();
        else{
            //this.fetchFinancialYear(this.finYR,0);
            this.$store.dispatch("fetchPartyMaster",0);
        }
        SocketEvent.on('update',(data)=>{
            console.log(data);
            this.newUpdate(data);
        })
    },
    methods:{
        printData(data){
            this.$emit('print',data);
        },
        changeCompany(){
            if(this.$store.state.companyName == 0){
                this.$store.dispatch('changeCompany',1);
                this.fetchFinancialYear(this.finYR,1);
                document.getElementById('companyName').innerText="Falcon Engineering";
                window.localStorage.setItem('Company',1);
                this.$store.dispatch("fetchPartyMaster",1);
            }else{
                this.$store.dispatch('changeCompany',0);
                this.fetchFinancialYear(this.finYR,0);
                document.getElementById('companyName').innerText="G.D. Electroplating Works";
                window.localStorage.setItem('Company',0);
                this.$store.dispatch("fetchPartyMaster",0);
            }   
        },
        fetchFinancialYear(newValue,newCompany){
            this.$store.dispatch("fetchFinancialYear",{"year":newValue,"company":newCompany});
        }
    },
    watch:{
        finYR:{
            immediate:true,
            handler(newVal){
                let company=window.localStorage.getItem('Company');
                if(company==null)
                    company=0;
                this.fetchFinancialYear(newVal,company);
                this.$store.dispatch("storeFY",this.sanitizeString(newVal));
            }
        }
    }
}
</script>
<style scoped>
    .dropdown-item{
        cursor: pointer;
    }
</style>