<template>
    <div class="row">
            <div class="col-md-12 mx-auto custom-scroll table-wrapper">
                <table class="table table-striped table-bordered table-sm" cellspacing="0" width="100%">
                <thead>
                    <tr>
                    <th class="th-sm">Sl.No
                    </th>
                    <th class="th-sm">Chalan Out No
                    </th>
                    <th class="th-sm">Chalan Out Date
                    </th>
                    <th class="th-sm">Chalan Out Quantity
                    </th>
                    <th class="th-sm">Rejection Quantity
                    </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(order,index) in orderDetails" :key="order.chalanOutID">
                        <td>{{index+1}}</td>
                        <td>{{order.chalanOutID}}</td>
                        <td>{{properDate(order.chalanOutDate)}}</td>
                        <td>{{order.qty}}</td>
                        <td><input type="number" v-model='order.rejQty' @change="edited(order)"></td>
                    </tr>
                </tbody>
                </table>
            </div>
    </div>
</template>
<script>
export default {
    name:'chalanOutTable',
    props:{
        orderDetails:{
            type:Object,
            required:true,
        },
    },
    methods:{
        edited(order){
            order.rejQty=Number(order.rejQty);
            this.$emit('changedorder',order);
        },
        properDate(date){
            return new Date(date).toLocaleDateString('en-GB');
        }
    }
}
</script>
<style scoped>
.custom-scroll{
    position: relative;
    height: 300px;
    overflow:auto;
}
.table-wrapper{
    display: block;
}
table input{
    outline: none;
    border: none;
}
</style>