import ApiCalls from '@/utils/api.js'
const deleteCheck={
    methods:{
        async partyMasterDeleteCheck(orderMasterUrl,chalanInUrl,searchQuery){
            let results=[];
            await ApiCalls.search(orderMasterUrl,'partyID',searchQuery).then((res)=>{
                results=res.data;
            });
            if(results.length>0)
                throw Error('Order Number : ' + results.map((data)=> data.orderNumber));
            await ApiCalls.search(chalanInUrl,'partyID',searchQuery).then((res)=>{
                results=res.data;
            });
            if(results.length>0)
                throw Error('ChalanIn Number : ' + results.map((data)=> data.chalanId));            
        },
        async orderMasterDeleteCheck(chalanInUrl,searchQuery){
            let results=[];
            await ApiCalls.search(chalanInUrl,'orderNo',searchQuery).then((res)=>{
                results=res.data;
            });
            if(results.length>0)
                throw Error('ChalanIn Number : ' + results.map((data)=> data.chalanId));       
        },
        async chalanInMasterDeleteCheck(chalanOutUrl,searchQuery){
            let results=[];
            await ApiCalls.search(chalanOutUrl,'chalanInId',searchQuery).then((res)=>{
                results=res.data;
            });
            if(results.length>0){
                let mapped=results.map((data)=> data.chalanOutID);
                let set=new Set();
                mapped.forEach(ele=>{
                    if(set.has(ele) == false)
                        set.add(ele);
                })
                throw Error('ChalanOut Number : ' + Array.from(set).join(','));
            }
        },
        async chalanOutMasterDeleteCheck(billDetailUrl,searchQuery){
            let results=[];
            await ApiCalls.search(billDetailUrl,'chalanOutId',searchQuery).then((res)=>{
                results=res.data;
            });
            if(results.length>0){
                let mapped=results.map((data)=> data.billNumber);
                let set=new Set();
                mapped.forEach(ele=>{
                    if(set.has(ele) == false)
                        set.add(ele);
                })
                throw Error('Bill Number : ' + Array.from(set).join(','));
            }
        }
    }
};

export default deleteCheck;