<template>
    <div class="row">
            <div class="col-md-12 mx-auto custom-scroll table-wrapper">
                <table class="table table-striped table-bordered table-sm" cellspacing="0" width="100%">
                <thead>
                    <tr>
                    <th class="th-sm">Sl.No
                    </th>
                    <th class="th-sm">ChalanIn No
                    </th>
                    <th class="th-sm">ChalanIn Date
                    </th>
                    <th class="th-sm">Plating
                    </th>
                    <th class="th-sm">Description
                    </th>
                    <th class="th-sm">Quantity
                    </th>
                    <th class="th-sm">Unit
                    </th>
                    <th class="th-sm">Rate
                    </th>
                    <th class="th-sm">Amount
                    </th>
                    <th class="th-sm">
                        Actions
                    </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="order in orderDetails" :key="order.chalanOutSl">
                        <td>{{order.chalanOutSl}}</td>
                        <td>{{order.chalanInId}}</td>
                        <td>{{properDate(order.chalanInDate)}}</td>
                        <td>{{order.platingType}}</td>
                        <td>{{order.desc1}}</td>
                        <td>{{order.qty}}</td>
                        <td>{{order.unit}}</td>
                        <td>{{order.rate}}</td>
                        <td>{{order.amount}}</td>
                        <editdelete :emitProperty="order" @edit="findBySerial" @delete="deleteValue"></editdelete>
                    </tr>
                </tbody>
                </table>
            </div>
    </div>
</template>
<script>
import editdelete from '../CommonComponents/EditDeleteTable'
export default {
    name:'chalanOutTable',
    components:{
        editdelete
    },
    props:{
        orderDetails:{
            type:Object,
            required:true,
        },
    },
    methods:{
        findBySerial(obj){
            this.$emit('find',obj);
        },
        deleteValue(obj){
            this.$emit('delete',obj.chalanOutSl);
        },
        properDate(date){
            return new Date(date).toLocaleDateString('en-GB');
        }
    }
}
</script>
<style scoped>
.custom-scroll{
    position: relative;
    height: 150px;
    overflow:auto;
}
.table-wrapper{
    display: block;
}
</style>