<template>
    <div class="row" style="margin-top:10px;">
        <div class="col-md-12 custom-scroll table-wrapper">
            <table class="table table-striped table-bordered table-sm " cellspacing="5" width="100%">
            <thead>
                <tr>
                <th class="th-sm">Party Name
                </th>
                <th class="th-sm">Party Type
                </th>
                <th class="th-sm">GSTIN
                </th>
                <th class="th-sm">Addressee
                </th>
                <th class="th-sm">Party Address
                </th>
                <th class="th-sm">Party Code
                </th>
                <th class="th-sm">Actions
                </th>                    
                </tr>
            </thead>
            <tbody id="PartyMasterTable">
                <tr v-for="pm in partydata" :key="pm.id">
                    <td>
                        {{pm.partyName}}
                    </td>
                    <td>
                        {{pm.partyType}}
                    </td>
                    <td>
                        {{pm.gstinNo}}
                    </td>
                    <td>
                        {{pm.addressee}}
                    </td>
                    <td>
                        {{pm.partyAddress1 +" "+pm.partyAddress2+" "+((pm.partyAddress3==null?"":pm.partyAddress3))}}
                    </td>
                    <td>
                        {{pm.partyCode}}
                    </td>
                    
                    <editdelete :emitProperty="pm.id" @edit="editButton" @delete="deleteButton"></editdelete>
                </tr>
            </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import editdelete from '../CommonComponents/EditDeleteTable'
export default {

    name:'partymastertable',
    components:{
        editdelete
    },
    props:{
        partydata:{
            type:Object,
            required:true,
        },
    },
    methods:{
        editButton(id){
            this.$emit('find',id);
        },
        deleteButton(id){
            this.$emit('delete',id);
        }
    }
}
</script>
<style scoped>
.custom-scroll{
    position: relative;
    height: 250px;
    overflow:auto;
}
.table-wrapper{
    display: block;
}
</style>