<template>
    <td>
        <div class="row">
            <div class="col-md-6">
                <button style="background-color:transparent; border-color:transparent;" title="Edit" @click="editEvent"> 
                    <img src="@/assets/edit.png" height="16"/>
                </button>
            </div>
            <button style="background-color:transparent; border-color:transparent;" title="Delete" @click="deleteEvent"> 
                <img src="@/assets/delete.png" height="16"/>
            </button>
        </div>
       
    </td>
</template>
<script>
export default {
    name:'editdelete',
    props:{
        emitProperty:{
            required:true,
        }
    },
    methods:{
        editEvent(){
            this.$emit('edit',this.emitProperty);
        },
        deleteEvent(){
            this.$emit('delete',this.emitProperty);
        },
    }

}
</script>