class ChalanInData{
    constructor(){
        this.chalanInMasterData={
            id:null,
            chalanId:'',
            chalanDate:null,
            finYR:'',
            partyID:null,
            orderNo:'VERBAL',
            orderDate:null,
            issueNo:'',
            issueDate:null,
            iNoteNo:'',
            iNoteDate:null,
            gpNo:'',
            gpDate:null,
            returnTag:1,
            cTag:0,
            ANX2:'',
        }
        this.copyData={...this.chalanInMasterData};
    }
    assignValues(data){
        this.chalanInMasterData={...data};
        return this.chalanInMasterData;
    }
    resetValue(){
        this.chalanInMasterData={...this.copyData};
        return this.chalanInMasterData;
    }
    validator(info){
        if(info.chalanId.length==0)
            throw new Error('Chalan Id');
        if(info.chalanDate == null)
            throw new Error('Chalan Date');
        else
            this.checkDateRange(info.chalanDate,info.finYR,"Chalan Date");
        if(info.issueDate != null)
            this.checkDateRange(info.issueDate,info.finYR,"Issue Date");
        if(info.iNoteDate != null)
            this.checkDateRange(info.iNoteDate,info.finYR,"I/Note Date");
        if(info.gpDate != null)
            this.checkDateRange(info.gpDate,info.finYR,"G.P Date");
        if(info.partyID==null)
            throw new Error('Party Name');
    }
    checkDateRange(date,finYR,fieldName){
        let len=finYR.length;
        let firstYear=2000+Number(finYR.slice(0,len/2));
        let secondYear=2000+Number(finYR.slice(len/2,len));
        date=new Date(date).setHours(0,0,0,0);
        let fromDate=new Date(firstYear,3,1);
        let toDate=new Date(secondYear,2,31);
        let inRange=(date>=fromDate) && (date<=toDate);
        if(!inRange)
            throw new Error(fieldName + " not in range: ="+firstYear.toString()+' - '+secondYear.toString()+" .");
    }
}

export default new ChalanInData();