import Vue from 'vue';
import VueRouter from 'vue-router';
import PartyMaster from '@/components/PartyMaster/PartyMaster.vue';
import OrderMaster from '@/components/OrderMaster/OrderMaster.vue'
import HomePage from '@/components/MainMenu/HomePage.vue';
import LoginPage from '@/components/MainMenu/LoginPage.vue';
import ChallanIn from '@/components/ChalanInMaster/ChallanIn.vue'
import ChallanOut from '@/components/ChalanOutMaster/ChallanOut.vue'
import BillEntry from '@/components/BillMaster/BillMaster.vue'
import RejectionAdjustment from '@/components/RejectionAdjustment/RejectionAdjustmentMaster.vue'
import RejectionChalanIn from '@/components/RejectionChalanIn/RejectionChalanIn.vue'

Vue.use(VueRouter);

const routes=[
    {
        path:'/login',
        component:LoginPage
    },
    {
        path:'/',
        component:HomePage,
        meta:{
            requiresAuth:true,
        }
    },
    {
        path:'/partyMaster',
        component:PartyMaster,
        meta:{
            requiresAuth:true,
        }
    },
    {
        path:'/orderMaster',
        component:OrderMaster,
        meta:{
            requiresAuth:true,
        }
    },
    {
        path:'/challanIn',
        component:ChallanIn,
        name:"ChallanIn",
        props:true,
        meta:{
            requiresAuth:true,
        }
    },
    {
        path:'/challanOut',
        component:ChallanOut,
        name:"ChallanOut",
        props:true,
        meta:{
            requiresAuth:true,
        }
    },
    {
        path:'/billEntry',
        component:BillEntry,
        name:"BillEntry",
        props:true,
        meta:{
            requiresAuth:true,
        }
    },
    {
        path:'/rejAdjustment',
        component:RejectionAdjustment,
        meta:{
            requiresAuth:true,
        }
    },
    {
        path:'/rejChalanIn',
        component:RejectionChalanIn,
        meta:{
            requiresAuth:true,
        }
    }
];
let vRouter=new VueRouter({
    routes,
});
vRouter.beforeEach((to,_,next)=>{
    const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
    if(requiresAuth && window.sessionStorage.getItem('user') === null){
        next('/login')
    }else
        next();
});
export default vRouter;