<template>
    <div class="container">

        <div class="row form-group no-gutters" style="margin-top:10px;">
            <label for="challanOutText" class="col-md-2 col-form-label text-center">Bill Entry No.:</label>
            <div class="col-md-4">
                <input type="text" id="challanOutText" class="form-control" v-model="currentSerial" disabled>
            </div>
            <div class="col-md-1">
                <button style="margin-left:10px;" type="button" class="btn btn-info" data-toggle="modal" data-target="#billmodal">&#8595;</button>
            </div>            
            <label for="datePicker" class="col-md-1 col-form-label text-center">Date:</label>
            <div class="col-md-3">
                <input id="datePicker" type="date" class="form-control" v-model="billMasterData.billDate">
            </div>
        </div>

        <div class="form-group row no-gutters">
            <label for="PartyName" class="col-md col-form-label text-center">Party Name:</label>
            <partydropdown :partyID.sync="billMasterData.partyID" :PartyMaster="PartyMaster"></partydropdown>

            <label for="PartyName" class="col-md col-form-label text-center">Buyer Name:</label>
            <partydropdown :partyID.sync="billMasterData.buyerMaster" :PartyMaster="PartyMaster"></partydropdown>

            <div class="col-md-1 mx-auto text-center">
                <button class="btn btn-primary" data-toggle="modal" data-target="#outmodal">+</button>
            </div>
        </div>

        <billmodal id="billmodal" :billData="PreviousBillDetail" @selectchalan="selectedBill"></billmodal>

        <outmodal id="outmodal" :chalanData="presetChalanOut" @selectchalan="selectedChalan"></outmodal>
        <hr>
        <billmastertable :orderDetails="billDetails" @find="findBill" @delete="deleteBill"></billmastertable>
        <hr>

        <div class="row form-group no-gutters">
            <label for="challanOutSelector" class="col-md-2 col-form-label text-center">Challan Out No.:</label>
            <div class="col-md-2">
                <input type="text" class="form-control" v-model="billDetailData.chalanOutId" disabled>
            </div>

            <label for="itemDescription" class="col-md-2 col-form-label text-center">Item Description:</label>
            <div class="col-md-6">
                <input type="text" class="form-control" v-model="billDetailData.desc1" disabled>
            </div>
        </div>

        <div class="row form-group no-gutters">
            <label for="challanOutDate" class="col-md-2 col-form-label text-center">Challan Out Date.:</label>
            <div class="col-md-2">
                <input id="datePicker" type="date" class="form-control" v-model="billDetailData.chalanOutDate" disabled>
            </div>
            <div class="col-md-2"></div>
            <div class="col-md-6">
                <input type="text" class="form-control" v-model="billDetailData.chalanInDetail.desc2" disabled>
            </div>
        </div>

        <div class="row form-group no-gutters">
            <label for="platingType" class="col-md-2 col-form-label text-center">Plating Type:</label>
            <div class="col-md-6">
                <input type="text" id="platingType" class="form-control" v-model="billDetailData.chalanOutDetail.platingType" disabled>
            </div>

            <label for="itemCode" class="col-md-2 col-form-label text-center">Item Code:</label>
            <div class="col-md-2">
                <input type="text" id="itemCode" class="form-control" v-model="billDetailData.chalanInDetail.itemCode" disabled>
            </div>
        </div>

        <div class="row form-group no-gutters">
            <label for="qtyType" class="col-md-1 col-form-label text-center">Qty:</label>
            <div class="col-md-1">
                <input type="number" id="qtyType" class="form-control" v-model="billDetailData.qty">
            </div>
            
            <label for="unitCode" class="col-md-1 col-form-label text-center">Unit:</label>
            <div class="col-md-1">
                <input type="text" id="unitCode" class="form-control" v-model="billDetailData.unit" disabled>
            </div>

            <label for="rateCode" class="col-md-1 col-form-label text-center">Rate:</label>
            <div class="col-md-1">
                <input type="number" id="rateCode" class="form-control" v-model="billDetailData.rate">
            </div>

            <label for="amountCode" class="col-md-1 col-form-label text-center">Amount:</label>
            <div class="col-md-1">
                <input type="number" id="amountCode" class="form-control" v-model="billDetailData.amount">
            </div>

            <div class="text-center col-md-4">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="saveValues">Save</button>
                    <button class="mx-2 btn btn-danger" @click="resetBottom">Clear</button>
                </div>
            </div>
            
        </div>

        <div class="row form-group no-gutters">
            <label for="cgst" class="col-md-1 col-form-label text-center">CGST %:</label>
            <div class="col-md-1">
                <input type="number" id="cgst" class="form-control" v-model="billMasterData.cgstRate">
            </div>
            
            <label for="cgstVal" class="col-md-1 col-form-label text-center">CGST Val:</label>
            <div class="col-md-1">
                <input type="number" id="cgstVal" class="form-control" v-model="billMasterData.cgstValue" disabled>
            </div>

            <label for="sgst" class="col-md-1 col-form-label text-center">SGST %:</label>
            <div class="col-md-1">
                <input type="number" id="sgst" class="form-control" v-model="billMasterData.sgstRate">
            </div>

            <label for="sgstVal" class="col-md-1 col-form-label text-center">SGST Val:</label>
            <div class="col-md-1">
                <input type="number" id="sgstVal" class="form-control" v-model="billMasterData.sgstValue">
            </div>

            <label for="totalGST" class="col-md-1 col-form-label text-center">Total GST:</label>
            <div class="col-md-1">
                <input type="number" id="totalGST" class="form-control" v-model="billMasterData.totalGST" disabled>
            </div>

            <label for="billValue" class="col-md-1 col-form-label text-center">Bill Val:</label>
            <div class="col-md-1">
                <input type="number" id="billValue" class="form-control" v-model="billMasterData.billValue" disabled>
            </div>

        </div>

        <div class="row">
            <div class="col-md-12 text-center">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="newButton">New</button>
                    <button class="mx-2 btn btn-danger" @click="updateButtonDialogueOpen" :disabled="!editing">Update</button>
                    <button class="mx-2 btn btn-danger" @click="deleteButtonDialogueOpen">Delete</button>
                    <button class="mx-2 btn btn-danger" @click="printWindow">Print</button>
                    <button class="mx-2 btn btn-danger" @click="navigateTo('/')" >Back</button>
                </div>
            </div>
        </div>
        <alertmodal id='alertmodal'  @updateVal="updateButton" @deleteVal="deleteButton" ref="alertmodal"></alertmodal>
    </div>
</template>
<script>
import navigation from '@/mixins/navigation.js'
import partydropdown from '../CommonComponents/DropDownParty'
import billmastertable from './BillMasterTable'
import outmodal from './BillDetailsAdderModal'
import billmodal from './BillModal'
import BillMasterData from '@/model/BillMaster.js'
import BillDetailData from '@/model/BillDetails.js'
import alertmodal from '../CommonComponents/Alerts';
import deleteCheck from '@/mixins/deleteCheck.js'
import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'

export default {
    name:"BillEntry",
    mixins:[navigation,deleteCheck],
    props:{
        initialData:{
            type:Object,
        }
    },
    components:{
        partydropdown,billmastertable,outmodal,billmodal,alertmodal
    },
    data(){
        return {
            presetChalanOut:[],
            billDetails:[],
            deleteBillDetails:[],
            billMasterData:BillMasterData.billMasterData,
            billDetailData:BillDetailData.billDetailData,
            editing:false,
        }
    },
    computed:{
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        },
        currentSerial:function(){
            let serial=this.$store.state.financialYearData.billSerial;
            if(this.editing)
                return this.billMasterData.billNo;            
            if(!serial)
                serial=0;
            serial+=1;
            return '0'.repeat(4-serial.toString().length)+serial.toString()+"/"+this.$store.state.year;
        },
        PreviousBillDetail:function(){
            let data = this.$store.state.financialYearData['bill_masters'];
            if(data == null)
                return [];
            return data;
        },
        URLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    chalanOutDetails:URLBuilder.buildURL(URLBuilder.endpoints.CHALANOUTDETAILS),
                    billMaster:URLBuilder.buildURL(URLBuilder.endpoints.BILLMASTER),
                    billDetails:URLBuilder.buildURL(URLBuilder.endpoints.BILLDETAILS),
                    chalanOutMaster:URLBuilder.buildURL(URLBuilder.endpoints.CHALANOUTMASTER),
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpoints.ORDERMASTER),
                }
            else
                return {
                    chalanOutDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANOUTDETAILS),
                    billMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.BILLMASTER),
                    billDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.BILLDETAILS),
                    chalanOutMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANOUTMASTER),
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERMASTER),
                }
        },
        finYR:function(){
            let newFinYR=this.$store.state.year;
            return newFinYR.split('-').join('');
        },
        alertDialogue:function(){
            return this.$refs.alertmodal;
        },
    },
    beforeDestroy(){
        this.resetAll();
    },
    mounted(){
        if(this.initialData != null)
            this.selectedBill(this.initialData);
    },
    methods:{
        selectedBill(bill){
            this.editing=true;
            this.resetBottom();
            this.billMasterData=bill;
            this.fetchBillDetails(bill.billNo);
        },
        selectedChalan(chalan){
            this.fetchChalanOutDetails(chalan.chalanOutID);
        },
        createBillDetail(data){
            data.forEach(element => {
                if(element.chalanOutData.qty != element.chalanOutData.billQty){
                    this.billDetailData=BillDetailData.assignValuesFromChalanOut(element);
                    this.billDetailData.billSlNo=this.billDetails.length+1;
                    this.billDetails.push({...this.billDetailData});
                    console.log(element);
                    this.resetBottom();
                }
            });            
            this.calculateNewBillValue();
        },
        findBill(bill){
            this.billDetailData=BillDetailData.assignValues(bill);
        },
        deleteBill(id){
            id=id-1;
            if(this.billDetails[id].id != null){
                this.deleteBillDetails.push(this.billDetails[id].id);
            }
            this.billDetails.splice(id,1);
            for(let i=0;i<this.billDetails.length;i++){
                this.billDetails[i].billSlNo=i+1;
            }
            this.calculateNewBillValue();
        },
        saveValues(){
            if(Number(this.billDetailData.billSlNo)>0){
                this.billDetails.splice(this.billDetailData.billSlNo-1,1, this.billDetailData);
                this.resetBottom();
                this.calculateNewBillValue();
            }
        },
        calculateAmount(){
            this.billDetailData.amount=Number(this.billDetailData.rate)*Number(this.billDetailData.qty);
        },
        resetBottom(){
            this.billDetailData=BillDetailData.resetValue();
        },
        resetTop(){
            this.billMasterData=BillMasterData.resetValue();
        },
        resetAll(){
            this.editing=false;
            this.presetChalanOut=[];
            this.billDetails=[];
            this.deleteBillDetails=[];
            this.resetTop();
            this.resetBottom();
        },
        calculateNewBillValue(){
            let sum=0;
            for(let x=0;x<this.billDetails.length;x++)
                sum+=Number(this.billDetails[x].amount);
            this.billMasterData.billValue=sum+this.billMasterData.totalGST;
            this.calculateTaxes();
        },
        calculateTaxes(){
            this.billMasterData.billValue-=this.billMasterData.totalGST
            this.billMasterData.cgstValue=this.billMasterData.billValue*Number(this.billMasterData.cgstRate)/100;
            this.billMasterData.sgstValue=this.billMasterData.billValue*Number(this.billMasterData.sgstRate)/100;
            this.billMasterData.totalGST=this.billMasterData.cgstValue+this.billMasterData.sgstValue;
            this.billMasterData.billValue+=this.billMasterData.totalGST;
            this.billMasterData.billRoundOff=Math.round(this.billMasterData.billValue);
            console.log( this.billMasterData.billRoundOff)
        },
        updateButtonDialogueOpen(){
            this.alertDialogue.update(this.billMasterData.billNo);
        },
        deleteButtonDialogueOpen(){
            if(this.billMasterData.id != null)
                this.alertDialogue.delete(this.billMasterData.billNo);
            else
                this.resetAll();
        },
        newButton(){
            if(this.billMasterData.id != null)
                this.resetAll();
            else
                this.createBillMaster();
        },
        updateButton(){
            this.updateBillMaster();   
            this.billUpdaterCreator();     
        },
        async printWindow(){
            let data={};
            data['master']=this.billMasterData;
            data['bills']=this.billDetails;
            if(this.billDetails[0].orderNumber != "VERBAL"){
                await ApiCalls.search(this.URLs.orderMaster,'orderNumber',this.billDetails[0].orderNumber).then(res=>{
                    data['order']=res.data[0];
                });
            }else{
                data['order']={orderNumber : "VERBAL", orderDate:null };
            }
            data['partyMaster']=this.PartyMaster[
                this.PartyMaster.map(ele=>{
                    return ele.id;
                }).indexOf(this.billMasterData.partyID)
            ];

            data['buyerMaster']=this.billMasterData.buyerMaster ? this.PartyMaster[
                this.PartyMaster.map(ele=>{
                    return ele.id;
                }).indexOf(this.billMasterData.buyerMaster)
            ] : data['partyMaster'];
            data['company']=this.$store.state.companyName;
            data['file']=0;
            window.localStorage.setItem('printData',JSON.stringify(data));
            this.$emit('print',{
                company:this.$store.state.companyName,
                page:'BillPrint'
            });
            console.log(data);
        },
        deleteButton(){
            let promises=[];
            if(this.billMasterData.id != null){
                for(const contents of this.billDetails){
                    promises.push(this.deleteBillDetail(contents.id));
                }
                promises.push(this.deleteBillMaster());
                if(this.deleteBillDetails.length>0){
                    for(const id of this.deleteBillDetails){
                        promises.push(this.deleteBillDetail(id));
                    }
                }
            }
            Promise.all(promises).then(()=>{
                this.alertDialogue.success(this.billMasterData.billNo,3);
                this.resetAll();
            }).catch(error=>{
                this.alertDialogue.error(error,3);
            });
        },
        updateFY(data,operation=0){
            // let fyData=this.$store.state.financialYearData;
            // let indexMaster=fyData["bill_masters"].map(ele=>ele.id).indexOf(data.id);
            // switch(operation){
            //     case 0:
            //         //New
            //         fyData.billSerial=fyData.billSerial==null?0+1:fyData.billSerial+1;
            //         fyData['bill_masters'].push(data);
            //         break;
            //     case 1:
            //         //Update
            //         fyData["bill_masters"].splice(indexMaster,1,data);
            //         break;
            //     case 2:
            //         fyData["bill_masters"].splice(indexMaster,1);
            //         break;
            // }            
            // this.$store.dispatch("updateChalanInMaster",fyData);
            console.log(operation);
        },
        fixDetail(contents){
            contents.billNumber=this.billMasterData.billNo;
            contents.billDate=this.billMasterData.billDate;
            contents.finYR=this.finYR;
            delete contents['chalanInDetail'];
            delete contents['chalanOutDetail'];
            return contents;
        },
        billUpdaterCreator(){
            let promises=[];
            if(this.deleteBillDetails.length>0){
                for(const id of this.deleteBillDetails){
                    promises.push(this.deleteBillDetail(id));
                }
            } 
            for(const contents of this.billDetails){
                let updatedDetail=this.fixDetail(contents);
                if(contents.id != null){
                    promises.push(this.updateBillDetail(contents));
                }
                else
                    promises.push(this.CreateBillDetail(updatedDetail));
            }
            Promise.all(promises).then(()=>{
                if(this.billMasterData.id != null)
                    this.alertDialogue.success(this.billMasterData.billNo,2);
                else
                    this.alertDialogue.success(this.billMasterData.billNo,1);
                this.resetAll();
            }).catch(error=>{
                this.alertDialogue.error(error,3);
            });
        },
        fetchChalanOutDetails(chalanOutID){
            ApiCalls.searchChalanOutId(this.URLs.chalanOutDetails+'/search/',chalanOutID).then((res)=>{
                this.createBillDetail(res.data);
            });
        },
        fetchBillDetails(billNo){
            ApiCalls.searchChalanOutId(this.URLs.billDetails+'/search/',billNo).then((res)=>{
                res.data.sort((a,b)=>{
                    return Number(a.billSlNo)-Number(b.billSlNo);
                });
                for(let x=1;x<=res.data.length;x++)
                    res.data[x-1].billSlNo=x;
                this.billDetails=res.data;
            });
        },
        updateBillMaster(){
            ApiCalls.update(this.URLs.billMaster+'/'+this.billMasterData.id,this.billMasterData).then((res)=>{
                console.log(res.data);
                //this.updateFY(res.data,1);
            });
        },
        async updateBillDetail(detail){
            await ApiCalls.update(this.URLs.billDetails+'/'+detail.id,detail).then((res)=>{
                console.log(res.data);
            });
        },
        async createBillMaster(){
            this.billMasterData.billNo=this.currentSerial;
            this.billMasterData.finYR=this.finYR;
            try{
                BillMasterData.validator(this.billMasterData);
            }catch(error){
                this.alertDialogue.error(error,2);
                return;
            }
            await ApiCalls.post(this.URLs.billMaster,this.billMasterData).then(async(res)=>{
                console.log(res.data);
               // this.updateFY(res.data);
                this.billUpdaterCreator();
            }).catch((error)=>{
                    this.alertDialogue.error(error,3);
            });
        },
        async CreateBillDetail(detail){
            await ApiCalls.post(this.URLs.billDetails,detail).then((res)=>{
                console.log(res.data);
            });
        },
        async deleteBillMaster(){
            await ApiCalls.delete(this.URLs.billMaster+"/"+this.billMasterData.id).then((res)=>{
                console.log(res.data);
                //this.updateFY(res.data,2);
            });
        },
        async deleteBillDetail(id){
            await ApiCalls.delete(this.URLs.billDetails+"/"+id).then((res)=>{
                console.log(res.data);
            });
        },
    },
    watch:{
        "billMasterData.partyID":async function(newValue){
            if(newValue === null)
                return;
            let prevYear=this.$store.state.year.split('-');
            for(let x=0;x<prevYear.length;x++){
                prevYear[x]-=1;
            }
            prevYear=prevYear.join('');
            let prevData=[];
            await ApiCalls.multiSearch(this.URLs.chalanOutMaster,[
                {
                    searchContent:"partyID",
                    searchString:newValue,
                },
                {
                    searchContent:"finYR",
                    searchString:prevYear,
                },
                {
                    searchContent:"billTag",
                    searchString:0,
                },
            ]).then(res=>{
                prevData=res.data;
            })
            this.presetChalanOut=this.$store.state.financialYearData['chalan_out_masters'].filter((chalan)=>{
                if(this.editing)
                    return chalan.partyID==newValue;
                else
                    return (chalan.partyID==newValue && (chalan.billTag == 0 || chalan.billTag == null));
            }).sort((a,b)=>{
                return Number(a.chalanOutID.split('/')[0])-Number(b.chalanOutID.split('/')[0]);
            })
            this.presetChalanOut=prevData.concat(this.presetChalanOut);
        },
        "billMasterData.cgstRate":function(){
            this.calculateTaxes();
        },
        "billMasterData.sgstRate":function(){
            this.calculateTaxes();
        },
        "billDetailData.rate":function(){
            this.calculateAmount();
        },
        "billDetailData.qty":function(){
            this.calculateAmount();
        }
    }
}
</script>