<template>
    <div  class="modal fade" role="dialog">
        <div class="modal-dialog">
            <div class="modal-content">
            <div class="modal-header">
                <h4 class="modal-title">Previous Bill Entries</h4>
            </div>
            <div class="modal-body">
                <div v-for="bill in billData" :key="bill.billNo" class="card" style="width: 50%;" @click="selectedOrder(bill)" data-dismiss="modal">
                    <div class="card-body">
                        <h5 class="card-title">{{bill.billNo}}</h5>
                        <p class="card-text">Date: {{properDate(bill.billDate)}}</p>
                    </div>
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
            </div>
            </div>

        </div>
    </div>
</template>
<script>
export default {
    name:'billmodal',
    props:{
        billData:{
            type:Object,
            required:true
        }
    },
    methods:{
        selectedOrder(data){
            this.$emit('selectchalan',data);
        },
        properDate(date){
            return new Date(date).toLocaleDateString('en-GB');
        }
    }
}
</script>
<style>
.card{
    cursor: pointer;
    float: left;
}
</style>