<template>
    <div  class="modal fade" role="dialog">
        <div class="modal-dialog modal-lg modal-dialog-centered">
            <div class="modal-content">
            <div class="modal-header">
                <h4 class="modal-title">Bills Against Order</h4>
            </div>
            <div class="modal-body">
                    <div class="row">
                        <div class="col-md-12">
                            <div class="row">
                                <label for="partyName" class="col-form-label text-center col-md-2">Party Name:</label>
                                <partydropdown :PartyMaster="PartyMaster" :partyID.sync="partyID"></partydropdown>
                                <label for="orderNumber" class="col-form-label text-center col-md-2">Order Number:</label>
                                <div class="col-md-4">
                                    <select id="orderNo" class="form-control" v-model="orderNumber" @change="selectedOrder">
                                        <option v-for="order in partyOrders" :key="order.orderNumber" :value="order.orderNumber">{{order.orderNumber}}</option>
                                    </select>
                                </div>
                            </div> 
                        </div>
                    </div>
                    <div class="row" style="margin-top:10px;">
                        <div class="col-md-12">
                            <div class="row" >
                                <label for="cgstRate" class="col-form-label text-center col-md-2">CGST Rate:</label>
                                <div class="col-md-4">
                                    <input type="number" class=" form-control" id="cgstRate" v-model="cgstRate">
                                </div>
                                <label for="sgstRate" class="col-form-label text-center col-md-2">SGST Rate:</label>
                                <div class="col-md-4">
                                    <input type="number" class="form-control" id="sgstRate" v-model="sgstRate">
                                </div>
                            </div>
                        </div>                                  
                    </div>    
                    
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-success" :disabled="validator" @click="fetchBillsAgainstOrder">Print</button>
                <button type="button" class="btn btn-danger" data-dismiss="modal">Close</button>
            </div>
            </div>

        </div>
    </div>
</template>
<script>
import partydropdown from '../CommonComponents/DropDownParty'
import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'
import $ from 'jquery';
export default {
    name:'ordermodal',
    components:{
       partydropdown
    },
    data(){
        return {
            partyID:null,
            partyOrders:[],
            orderNumber:null,
            cgstRate:0,
            sgstRate:0,
            orderMaster:null,
        }   
    },
    mounted(){
        $('#orderbills').on('hidden.bs.modal',()=>{
            this.partyID=null;
            this.partyOrders=[];
            this.orderNumber=null;
            this.cgstRate=0;
            this.sgstRate=0;
            this.orderMaster=null;
        });
    },
    computed:{
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        }, 
        validator:function(){
            return this.orderNumber == null;
        },
        URLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpoints.ORDERMASTER),
                    billMaster:URLBuilder.buildURL(URLBuilder.endpoints.BILLMASTER),
                }
            else
                return {
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERMASTER),
                    billMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.BILLDETAILS),
                }
        },
    },
    methods:{
        selectedOrder(event){
            let val=event.target.value;
            console.log(val);
            this.orderMaster=this.partyOrders[this.partyOrders.map(order=>order.orderNumber).indexOf(val)];
            console.log(this.orderMaster);
        },
        async fetchBillsAgainstOrder(){
            await ApiCalls.searchChalanOutId(this.URLs.billMaster+'/searchAgainstOrder/',this.orderNumber).then(res=>{
                let rateCgst=(this.cgstRate == 0)?false:true;
                let rateSgst=(this.sgstRate == 0)?false:true;                
                let filteredArray=res.data.filter((bill)=>{
                    if(rateCgst)
                        if(rateSgst)
                            return bill.cgstRate==this.cgstRate && bill.sgstRate==this.sgstRate;
                        else
                            return bill.cgstRate==this.cgstRate;
                    else if(rateSgst)
                        return bill.sgstRate==this.sgstRate;
                    else
                        return true;
                });
                console.log(filteredArray);
                let party=this.PartyMaster[this.PartyMaster.map(pm=>pm.id).indexOf(this.partyID)];
                let printData={
                    partyName:party,
                    order:this.orderMaster,
                    bills:filteredArray,
                    file:5,
                };
                window.localStorage.setItem('printData',JSON.stringify(printData));
                this.$emit('print',{
                    company:this.$store.state.companyName,
                    page:'OrderBills'
                });
            });
        },
        fetchPartyOrders(){
            ApiCalls.search(this.URLs.orderMaster,"partyID",this.partyID).then((res)=>{
                this.partyOrders=res.data;
            }).catch(error=>{
                console.log(error);
            })
        },
    },
    watch:{
        partyID:function(newValue){
            if(newValue != null)
                this.fetchPartyOrders();
        }
    }
}
</script>