class ChalanOutMasterData{
    constructor(){
        this.chalanOutMasterData={
            id:null,
            chalanOutID:'',
            outDate:null,
            partyID:null,
            packing:'',
            through:'',
            rrNo:'',
            lrNo:'',
            cargo:'',
            billTag:0,
            cgstRate:0,
            cgstValue:0,
            sgstRate:0,
            sgstValue:0,
            totalGST:0,
            billValue:0,
            billRoundOff:0,
            finYR:'',
            buyerMaster:null
        }
        this.copyData={...this.chalanOutMasterData};
    }
    assignValues(data){
        this.chalanOutMasterData={...data};
        return this.chalanOutMasterData;
    }
    resetValue(){
        this.chalanOutMasterData={...this.copyData};
        return this.chalanOutMasterData;
    }
    validator(info){
        if(info.chalanOutID.length==0)
            throw new Error('Description');
        if(info.outDate == null)
            throw new Error('Chalan Out Date');
        else
            this.checkDateRange(info.outDate,info.finYR,"Chalan Out Date")
        if(info.partyID == null)
            throw new Error('Party Name');
    }
    checkDateRange(date,finYR,fieldName){
        let len=finYR.length;
        let firstYear=2000+Number(finYR.slice(0,len/2));
        let secondYear=2000+Number(finYR.slice(len/2,len));
        date=new Date(date).setHours(0,0,0,0);
        let fromDate=new Date(firstYear,3,1);
        let toDate=new Date(secondYear,2,31);
        let inRange=(date>=fromDate) && (date<=toDate);
        if(!inRange)
            throw new Error(fieldName + " not in range: ="+firstYear.toString()+' - '+secondYear.toString()+" .");
    }
}

export default new ChalanOutMasterData();