<template>
    <div class="row">
            <div class="col-md-12 mx-auto custom-scroll table-wrapper">
                <table class="table table-striped table-bordered table-sm" cellspacing="0" width="100%">
                <thead>
                    <tr>
                    <th class="th-sm">Sl.No
                    </th>
                    <th class="th-sm">ChalanOut
                    </th>
                    <th class="th-sm">ChalanOutDate
                    </th>
                    <th class="th-sm">Plating
                    </th>
                    <th class="th-sm">Description
                    </th>
                    <th class="th-sm">Code
                    </th>
                    <th class="th-sm">Quantity
                    </th>
                    <th class="th-sm">Unit
                    </th>
                    <th class="th-sm">
                        Actions
                    </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="detail in orderDetails" :key="detail.billSlNo">
                        <td>{{detail.billSlNo}}</td>
                        <td>{{detail.chalanOutId}}</td>
                        <td>{{properDate(detail.chalanOutDate)}}</td>
                        <td>{{detail.chalanOutDetail.platingType}}</td>
                        <td>{{detail.desc1}}</td>
                        <td>{{detail.chalanInDetail.itemCode==null?"":detail.chalanInDetail.itemCode}}</td>
                        <td>{{detail.qty}}</td>
                        <td>{{detail.unit}}</td>
                        <editdelete :emitProperty="detail" @edit="findBySerial" @delete="deleteValue"></editdelete>
                    </tr>
                </tbody>
                </table>
            </div>
    </div>
</template>
<script>
import editdelete from '../CommonComponents/EditDeleteTable'
export default {
    name:'chalanOutTable',
    components:{
        editdelete
    },
    props:{
        orderDetails:{
            type:Object,
            required:true,
        },
    },
    methods:{
        findBySerial(obj){
            this.$emit('find',obj);
        },
        deleteValue(obj){
            this.$emit('delete',obj.billSlNo);
        },
        properDate(date){
            return new Date(date).toLocaleDateString('en-GB');
        }
    }
}
</script>
<style scoped>
.custom-scroll{
    position: relative;
    height: 150px;
    overflow:auto;
}
.table-wrapper{
    display: block;
}
</style>