<template>
    <div class="container">
        <partymastertable :partydata="PartyMaster" @find='findById' @delete='deleteValueButton'></partymastertable>
        <hr>
        <form>
        <div class="form-group row no-gutters">
            <label for="partyNameText" class="col-md-2 col-form-label text-center">Party Name:</label>
            <div class="col-md-6">
                <input type="text" id="partyNameText" v-model="currentParty.partyName" class="form-control">
            </div>
        </div>
        <div class="form-group row no-gutters">
            <label for="addresseText" class="col-md-2 col-form-label text-center" >Addresee:</label>
            <div class="col-md-3">
                <input type="text" id="addresseText" class="form-control" v-model="currentParty.addressee">
            </div>
            <label for="gstinText" class="col-md-2 col-form-label text-center">GSTIN No.:</label>
            <div class="col-md-3">
                <input type="text" id="gstinText" class="form-control" v-model="currentParty.gstinNo">
            </div>
        </div>
        <div class="form-group row no-gutters">
            <label for="typeText" class="col-md-2 col-form-label text-center">Type:</label>
            <div class="col-md-3">
                <select id="typeText" class="form-control" v-model="currentParty.partyType">
                    <option value="GOVT.">Govt.</option>
                    <option value="OTHER">Other</option>
                </select>
            </div>
            <label for="allotedText" class="col-md-2 col-form-label text-center">Alloted Code:</label>
            <div class="col-md-3">
                <input type="text" id="allotedText" class="form-control" v-model="currentParty.partyCode">
            </div>
        </div>
        <div class="form-group row no-gutters">
            <label for="address" class="col-md-2 col-form-label text-center">Address:</label>                
            <div class="col-md-3">
                <input type="text" id="address" class="form-control" v-model="currentParty.partyAddress1">
            </div>
        </div>
        <div class="form-group row no-gutters">  
            <span class="col-md-2"></span>              
            <div class="col-md-3">
                <input type="text" id="address2" class="form-control" v-model="currentParty.partyAddress2">
            </div>
        </div>    
        <div class="form-group row no-gutters"> 
            <span class="col-md-2"></span>          
            <div class="col-md-3">
                <input type="text" id="address3" class="form-control" v-model="currentParty.partyAddress3">
            </div>
        </div>        
        </form>    

        <div class="row">
            <div class="col-md-12 text-center">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" :disabled="editing" @click="postValue()" >New</button>
                    <button class="mx-2 btn btn-danger" :disabled="!editing" @click="updateValueButton()">Update</button>
                    <button class="mx-2 btn btn-danger" @click="resetValues()">Clear</button>
                    <button class="mx-2 btn btn-danger" @click="navigateTo('/')">Back</button>
                </div>                
            </div>
        </div>  

        <alertmodal id='alertmodal'  @updateVal="updateValue" @deleteVal="deleteValue" ref="alertmodal"></alertmodal>
    </div>    
</template>

<script>
import navigation from '@/mixins/navigation.js'
import deleteCheck from '@/mixins/deleteCheck.js'
import ApiCalls from '@/utils/api.js'
import URLBuilder from '@/utils/URLBuild.js'

import PartyInformation from '@/model/PartyMaster.js'
import alertmodal from '../CommonComponents/Alerts';
import partymastertable from './PartyMasterTable';

export default {
    name:'PartyMaster',
    mixins:[navigation,deleteCheck],
    components:{
        partymastertable,alertmodal
    },
    data(){
        return{
            currentParty:PartyInformation.partyInfos,
            editing:false,
        }
    },
    computed:{
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        },
        URLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    partyMaster:URLBuilder.buildURL(URLBuilder.endpoints.PARTYMASTER),
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpoints.CHALANINMASTER),
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpoints.ORDERMASTER),
                };
            else
                return {
                    partyMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.PARTYMASTER),
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANINMASTER),
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERMASTER),
                };
        },
        alertDialogue:function(){
            return this.$refs.alertmodal;
        }
    },
    methods:{
       findById(id){
           let party=this.PartyMaster.filter((pm)=>pm.id==id)[0];
           this.assignValues(party);
           this.editing=true;
       },
       assignValues(party){
           this.currentParty=PartyInformation.assignValues(party);
       },
       resetValues(){           
           this.currentParty=PartyInformation.reset();
           this.editing=false;
       },
       async deleteValueButton(id){
           let mappedValue=this.PartyMaster.map(data=>data.id);      
           try{
               await this.partyMasterDeleteCheck(this.URLs.orderMaster,this.URLs.chalanInMaster,id);
           }     
           catch(error){
               this.alertDialogue.error(error,1);
               return;
           }
           this.alertDialogue.delete(this.PartyMaster[mappedValue.indexOf(id)].partyName,id);           
       },
       updateValueButton(){
           let mappedValue=this.PartyMaster.map(data=>data.id);           
           this.alertDialogue.update(this.PartyMaster[mappedValue.indexOf(this.currentParty.id)].partyName);
       },
       postValue(){
            try{
                PartyInformation.validator(this.currentParty);
            }catch(error){
                this.alertDialogue.error(error,2);
                return;
            }
            ApiCalls.post(this.URLs.partyMaster,this.currentParty).then((res)=>{               
                this.$store.dispatch("fetchPartyMaster",this.$store.state.companyName);
                this.resetValues();
                this.alertDialogue.success(res.data['partyName'],1);
            }).catch(error=>{
                this.alertDialogue.error(error,3);
            });  
       },
       updateValue(){   
           ApiCalls.update(this.URLs.partyMaster+"/"+this.currentParty.id,this.currentParty).then((res)=>{               
               this.$store.dispatch("fetchPartyMaster",this.$store.state.companyName);
               this.resetValues();
               this.alertDialogue.success(res.data['partyName'],2);
           }).catch(error=>{
               this.alertDialogue.error(error,3);
           });        
       },
       deleteValue(id){
           ApiCalls.delete(this.URLs.partyMaster+"/"+id).then((res)=>{
               this.$store.dispatch("fetchPartyMaster",this.$store.state.companyName);
               this.alertDialogue.success(res.data['partyName'],3);
           }).catch(error=>{
               this.alertDialogue.error(error,3);
           }); 
       }
    },
}
</script>
<style scoped>
</style>