<template>
    <div  class="modal fade" role="dialog" id="showModal">
        <div class="modal-dialog">
            <div class="modal-content">
                <div v-if="type==1" class="modal-header" style="background:green;">
                    <h4  class="modal-title" >Success</h4>
                </div>
                <div v-else-if="type==2" class="modal-header" style="background:red;">
                    <h4 class="modal-title">Error</h4>
                </div>
                <div v-else class="modal-header" style="background:#f59342;">
                    <h4 class="modal-title">Warning</h4>
                </div>
            <div class="modal-body">
                <p>{{this.message}}</p>
            </div>
                <div v-if="type == 1 || type == 2" class="modal-footer">
                    <button type="button" class="btn btn-default btn-success" data-dismiss="modal">Close</button>
                </div>
                <div v-else class="modal-footer">
                    <button type="button" class="btn btn-default btn-success" data-dismiss="modal">Close</button>
                    <button v-if="type == 3" type="button" class="btn btn-default btn-danger" @click="emitData('deleteVal')" data-dismiss="modal">Delete</button>
                    <button v-else type="button" class="btn btn-default btn-danger" @click="emitData('updateVal')" data-dismiss="modal">Update</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import $ from "jquery";
export default {
    name:'alertmodal',
    data(){
        return {
            message:'',
            type:0,
            emitVal:null,
            notificationStack:[],
            showing:false,
        }
    },
    mounted(){
         $('#alertmodal').on('hidden.bs.modal',()=>{
            this.emitVal=null;
            this.showing=false;
            if(this.notificationStack.length>0){
                this.message=this.notificationStack[0].message;
                this.type=this.notificationStack[0].type;
                this.notificationStack.splice(0,1);
                this.show();
            }
         })
    },
    methods:{
        // 1 - Exists Cant Delete
        // 2 - Field Empty
        // 3 - Network Error
        error(message,type){
            let msg=message.toString();
            switch(type){
                case 1:
                    this.message=message+' Already Exists';
                    break;
                case 2:      
                console.log(msg.charAt(0))              
                    if(msg.indexOf('=')>=0){
                        this.message=msg.split('=').join('');
                    }
                    else{
                        this.message=message +' Field Empty';
                    }
                    break;
                case 3:
                    this.message=message;
                    break;
            }
            this.type=2;
            this.show();
        },
        delete(message,emitVal=null){
            this.message='Are you sure you want to delete '+message+'?';
            this.type=3;
            this.emitVal=emitVal==null?null:emitVal;
            this.show();
        },
        update(message){
            this.message='Are you sure you want to update '+message+'?';
            this.type=4;
            this.show();
        },
        success(message,type){
            let newMessage='';
            switch(type){
                case 1:
                    newMessage=message+' posted successfully';
                    break;
                case 2:
                    newMessage=message+' updated successfully';
                    break;
                case 3:
                    newMessage=message+' deleted successfully';
                    break;
            }
            if(this.showing)
                this.notificationStackAdd(newMessage,1);
            else{
                this.message=newMessage;
                this.type=1;
                this.show();
            }
        },
        emitData(eventName){
            if(this.emitVal==null)
                this.$emit(eventName);
            else
                this.$emit(eventName,this.emitVal);
        },
        notificationStackAdd(message,type){
            this.notificationStack.push({
                message:message,
                type:type,
            });
        },
        show(){
            $('#alertmodal').modal('toggle');
            this.showing=true;
        }
    }
}
</script>
