<template>
<div class="container" >
        <div class="row form-group no-gutters" style="margin-top:10px;">
            <label for="newNumber" class="col-md-2 col-form-label text-center">Challan In No.:</label>
            <div class="col-md-4" v-if="disableChalan">
                <input type="text" id="newNumber" class="form-control"  :disabled='disableChalan' v-model="currentOrderSerial">
            </div>
            <div class="col-md-4" v-else>
                <input type="text" id="newNumber" class="form-control"   v-model="chalanInData.chalanId">
            </div>

            <div class="col-md">
                <button style="margin-left:10px;" type="button" class="btn btn-info" data-toggle="modal" data-target="#myModal">&#8595;</button>
                <button style="margin-left:10px;" type="button" class="btn btn-info" @click="disableChalan=!disableChalan;" :disabled='editing'>Insert</button>
            </div>
            

            <chalanmodal id="myModal" :chalanData="ChalanData" @selectchalan="selectedChalan"></chalanmodal>

            <label for="datePicker" class="col-md-1 col-form-label text-center">Date:</label>
            <div class="col-md-3">
                <input id="datePicker" type="date" class="form-control" v-model="chalanInData.chalanDate">
            </div>
        </div>
        <div class="form-group row no-gutters">
            <label for="PartyName" class="col-md-2 col-form-label text-center">Party Name:</label>
            <partydropdown :PartyMaster="PartyMaster" :partyID.sync="chalanInData.partyID"></partydropdown>

            <label for="annexText" class="col-md-2 col-form-label text-center">Annex II</label>
            <div class="col-md-3">
                <input type="text" id="annexText" class="form-control" v-model="chalanInData.ANX2" >
            </div>
        </div>
        <div class="form-group row no-gutters">
            <label for="orderNo" class="col-md-2 col-form-label text-center">Order No.:</label>
            <div class="col-md-2">
                <select id="orderNo" class="form-control" v-model="chalanInData.orderNo">
                    <option value="VERBAL">Verbal</option>
                    <option v-for="order in partyOrders" :key="order.orderNumber" :value="order.orderNumber">{{order.orderNumber}}</option>
                </select>
            </div>
            <div class="col-md-2">
                <input id="datePicker" type="date" class="form-control" v-model="chalanInData.orderDate">
            </div>
            <label for="issueVoucherNo" class="col-md-2 col-form-label text-center">Issue No.:</label>
            <div class="col-md-2">
                <input type="text" id="issueVoucherNo" class="form-control"  v-model="chalanInData.issueNo">
            </div>
            <div class="col-md-2">
                <input id="datePicker" type="date" class="form-control" v-model="chalanInData.issueDate">
            </div>
        </div>
        <div class="form-group row no-gutters">
            <label for="gatePass" class="col-md-2 col-form-label text-center" >Gate Pass No.</label>
            <div class="col-md-2">
                <input type="text" id="gatePass" class="form-control" v-model="chalanInData.gpNo">
            </div>
            <div class="col-md-2">
                <input id="datePicker" type="date" class="form-control" v-model="chalanInData.gpDate">
            </div>
            <label for="iNoteNo" class="col-md-2 col-form-label text-center">I/Note No.</label>
            <div class="col-md-2">
                <input type="text" id="iNoteNo" class="form-control" v-model="chalanInData.iNoteNo">
            </div>
            <div class="col-md-2">
                <input id="datePicker" type="date" class="form-control" v-model="chalanInData.iNoteDate">
            </div>
        </div>

    <hr>
    
    <chalanintable :orderDetails="chalanDetails" :platingDetails="Platings" @find='selectedChalanIN' @delete="deleteValue"></chalanintable>

    <hr>

        <div class="row form-group no-gutters">
            <label for="itemDescription" class="col-md-2 col-form-label text-center">Item Description:</label>

            <div class="col-md-10" v-if="orderCheck">
                <input type="text" list="orderDetails" id="itemDescription" class="form-control" v-model="chalanInDetailData.desc1" :disabled="this.chalanInDetailData.id!==null" >
                <datalist id="orderDetails">
                    <option v-for="order in orderDetails" :key="order.orderSerial">{{order.desc1}}</option>
                </datalist>
            </div>
            <div class="col-md-10" v-else>
                <input type="text" id="itemDescription" class="form-control" v-model="chalanInDetailData.desc1" :disabled="this.chalanInDetailData.id!==null">
            </div>

        </div>

        <div class="form-group row no-gutters">  
            <span class="col-md-2"></span>              
            <div class="col-md-10">
                <input type="text" id="itemDescription2" class="form-control" v-model="chalanInDetailData.desc2">
            </div>
        </div>
 
        <div class="form-group row no-gutters">
            <label for="PlatingType" class="col-md-2 col-form-label text-center">Plating Type:</label>
            <platingtype :platingID.sync="chalanInDetailData.platingId" :Platings="Platings"></platingtype>

            <label for="itemCode" class="col-md-2 col-form-label text-center" >Item Code:</label>
            <div class="col-md-2">
                <input type="text" id="itemCode" class="form-control" v-model="chalanInDetailData.itemCode">
            </div>
        </div>
        <div class="form-group row no-gutters">
            <label for="quantity" class="col-md-2 col-form-label text-center">Quantity:</label>
            <div class="col-md-2">
                <input type="text" id="quantity" class="form-control" v-model="chalanInDetailData.qty">
            </div>
            <label for="unit" class="col-md-1 col-form-label text-center mx-auto">Unit:</label>
            <unitdropdown :unitType.sync="chalanInDetailData.unit" :Units="Units"></unitdropdown>
            <div class="col-md-5 text-center">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="addToChalanInList">Add</button>
                    <button class="mx-2 btn btn-danger" @click="resetBottom">Clear</button>
                </div>
            </div>
        </div>

        <div class="row">
            <div class="col-md-12 text-center">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="newButton">New</button>
                    <button class="mx-2 btn btn-danger" :disabled="!editing" @click="updateButtonDialogueOpen" >Update</button>
                    <button class="mx-2 btn btn-danger" @click="deleteButton">Delete</button>
                    <button class="mx-2 btn btn-danger" @click="navigateTo('/')" >Back</button>
                </div>
            </div>
        </div>

        <alertmodal id='alertmodal'  @updateVal="updateButton" @deleteVal="deleteChalanEntry" ref="alertmodal"></alertmodal>
</div>
</template>

<script>
import navigation from '@/mixins/navigation.js'
import partydropdown from '../CommonComponents/DropDownParty'
import unitdropdown from '../CommonComponents/DropDownUnit'
import platingtype from '../CommonComponents/DropDownPlating'
import chalanintable from './ChlanInTable'
import chalanmodal from './ChalanInModal'
import ChalanInMasterData from '@/model/RejectionChalanInMaster.js'
import ChalanInDetailData from '@/model/RejectionChalanInDetail.js'
import alertmodal from '../CommonComponents/Alerts';
import deleteCheck from '@/mixins/deleteCheck.js'
import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'

export default {
    name:'ChallanIn',
    components:{
        partydropdown,chalanintable,chalanmodal,platingtype,unitdropdown,alertmodal
    },
    mixins:[navigation,deleteCheck],
    data(){
        return {
            chalanInData:ChalanInMasterData.chalanInMasterData,
            chalanInDetailData:ChalanInDetailData.chalanDetailData,
            orderDetails:[],//Order Details of the order
            disableChalan:true,
            editing:false,
            partyOrders:[],//Orders of the Party
            chalanDetails:[],//Chalan In Details of the ChalanID
            deleteChalan:[],
        }
    },
    computed:{
        alertDialogue:function(){
            return this.$refs.alertmodal;
        },
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        },
        Units:function(){
            return this.$store.state.units;
        },
        Platings:function(){
            if(this.$store.state.companyName == 0)
                return this.$store.state.platingID["RECORD"];
            else
                return this.$store.state.platingID["PLATINGMST"];
        },
        currentOrderSerial:function(){
            let serial=this.$store.state.financialYearData.chalanInSerial;
            if(this.editing)
                return this.chalanInData.chalanId;
            if(!serial)
                serial=0;
            serial+=1;
            return '0'.repeat(4-serial.toString().length)+serial.toString()+"/"+this.$store.state.year;
        },
        ChalanData:function(){
            let data = this.$store.state.financialYearData['chalan_in_masters'];
            if(data==null)
                return [];
            data=data.filter(data=>{
                return data.returnTag==1;
            });
            if(data.length>0){
                return data.sort((a,b)=>{
                    return new Number(a.chalanId.split('/')[0])-new Date(b.chalanId.split('/')[0]);
                });
            }else
                return data;
        },
        orderCheck:function(){
            return this.chalanInData.orderNo !== "VERBAL";
        },
        URLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpoints.CHALANINMASTER),
                    chalanInDetails:URLBuilder.buildURL(URLBuilder.endpoints.CHALANINDETAILS),
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpoints.ORDERMASTER),
                    orderDetails:URLBuilder.buildURL(URLBuilder.endpoints.ORDERDETAILS),
                    chalanOutDetails:URLBuilder.buildURL(URLBuilder.endpoints.CHALANOUTDETAILS)
                }
            else
                return {
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANINMASTER),
                    chalanInDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANINDETAILS),
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERMASTER),
                    orderDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERDETAILS),
                    chalanOutDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANOUTDETAILS)
                }
        },
        finYR:function(){
            let newFinYR=this.$store.state.year;
            return newFinYR.split('-').join('');
        }
    },
    beforeDestroy(){
        this.resetAll();     
    },
    methods:{     
        addToChalanInList(){
            try{
                ChalanInDetailData.validator(this.chalanInDetailData);
            }catch(error){
                this.alertDialogue.error(error,2);
                return;
            }
            //let index=this.findCorrespoindingOrderIndex(null,this.chalanInDetailData.orderID);
            if(this.chalanInDetailData.chalanSlNo==0){
                this.chalanInDetailData.chalanSlNo=this.chalanDetails.length+1;
                this.chalanDetails.push({...this.chalanInDetailData});                
               // if(index >= 0)
               //     this.orderDetails[index]=this.updateOrderQuantity(this.orderDetails[index],this.chalanInDetailData.qty,null);
            }else{
                let delIndex=this.chalanInDetailData.chalanSlNo-1;
               // let deletQty=this.chalanDetails[delIndex].qty;
                this.chalanDetails.splice(delIndex,1,{...this.chalanInDetailData});
               // if(index >= 0)
               //     this.orderDetails[index]=this.updateOrderQuantity(this.orderDetails[index],this.chalanInDetailData.qty,deletQty);
            }
            this.resetBottom();
        },
        findCorrespoindingOrderIndex(desc1=null,id=null){
            if(desc1 !== null)
                return this.orderDetails.map(order=>order.desc1).indexOf(desc1);
            return this.orderDetails.map(order=>order.id).indexOf(id);
        }, 
        updateOrderQuantity(order,qty,deleteQty=null){
            let newQty=Number(order.chalQty);
            if(deleteQty != null)
                newQty-=Number(deleteQty);
            newQty+=Number(qty)
            order.chalQty=newQty;
            console.log(order.chalQty);
            return order;
        },
        deleteValue(id){
            id=id-1;
            if(this.chalanDetails[id].id != null){
                let index=this.findCorrespoindingOrderIndex(this.chalanDetails[id].desc1);
                this.deleteChalan.push({"id":this.chalanDetails[id].id,"correspondingOrder":index,"delQty":this.chalanDetails[id].qty});
                console.log(this.deleteChalan);
            }

            this.chalanDetails.splice(id,1);
            for(let i=0;i<this.chalanDetails.length;i++){
                this.chalanDetails[i].chalanSlNo=i+1;
            }
        },  
        selectedChalan(chalan){
            this.disableChalan=true;
            this.editing=true;
            this.resetBottom();
            this.chalanInData=ChalanInMasterData.assignValues(chalan);
            this.currentOrderSerial=this.chalanInData.chalanId;
            this.fetchChalanDetails();
            if(this.orderCheck)
                this.fetchOrderDetails();
            console.log(chalan);
        },
        selectedChalanIN(chalan){
            this.chalanInDetailData=ChalanInDetailData.assignValues(chalan);
        },
        resetBottom(){
            this.chalanInDetailData=ChalanInDetailData.resetValue();
        },
        resetTop(){
            this.chalanInData=ChalanInMasterData.resetValue();
        },
        resetAll(){
            this.editing=false;
            this.partyOrders=[];
            this.chalanDetails=[];
            this.deleteChalan=[];
            this.resetTop();
            this.resetBottom();
        },
        updateButtonDialogueOpen(){
            this.alertDialogue.update(this.chalanInData.chalanId);
        },
        newButton(){
            if(this.chalanInData.id != null)
                this.resetAll();
            else
                this.createNewChalanIn();
        },
        updateButton(){
            this.updateChalanMaster(this.chalanInData);
            this.chalanDetailsUpdaterCreator(this.chalanInData.chalanId,this.chalanInData.chalanDate);
            if(this.deleteChalan.length>=0){
                this.deleteChalan.forEach(element=>this.deleteChalanDetail(element));
            }
        },
        async deleteButton(){
            if(this.chalanInData.id !== null){
                try{
                    await this.chalanInMasterDeleteCheck(this.URLs.chalanOutDetails,this.chalanInData.chalanId);
                }catch(error){
                    this.alertDialogue.error(error,1);
                    return;
                }
                this.alertDialogue.delete(this.chalanInData.chalanId);
            }
            else
                this.resetAll();
        },
        chalanDetailsUpdaterCreator(id,date){
            let promises=[];
            this.chalanDetails.forEach(chalan => {
                if(chalan.id != null)
                    promises.push(this.updateChalanDetail(chalan,date));
                else
                    promises.push(this.createChalanDetail(chalan,id,date));
            });
            Promise.all(promises).then(()=>{
                if(this.chalanInData.id==null)
                    this.alertDialogue.success(this.chalanInData.chalanId,1);
                else
                    this.alertDialogue.success(this.chalanInData.chalanId,2);
                this.resetAll();
            }).catch(error=>{
                this.alertDialogue.error(error,3);
                return;
            });
        },
        createNewChalanIn(){
            this.chalanInData.chalanId=this.disableChalan?this.currentOrderSerial:this.chalanInData.chalanId;
            this.chalanInData.finYR=this.finYR;
            try{
                ChalanInMasterData.validator(this.chalanInData)
            }catch(error){
                this.alertDialogue.error(error,2);
                return;
            }
            ApiCalls.post(this.URLs.chalanInMaster,this.chalanInData).then((res)=>{
                console.log(res.data);
                this.chalanDetailsUpdaterCreator(this.chalanInData.chalanId,this.chalanInData.chalanDate);
                let fyData=this.$store.state.financialYearData;
                fyData.chalanInSerial=fyData.chalanInSerial==null?0+1:fyData.chalanInSerial+1;
                fyData['chalan_in_masters'].push(res.data);
                this.$store.dispatch("updateChalanInMaster",fyData);
            }).catch(error=>{
                this.alertDialogue.error(error,3);
                return;
            });
        },
        async createChalanDetail(chalan,id,date){
            chalan.chalanId=id;
            chalan.chalanInDate=date;
            await ApiCalls.post(this.URLs.chalanInDetails,chalan).then((res)=>{
                console.log(res.data);
                // if(chalan.orderID !== null)
                //     this.updateOrderDetails(chalan.orderID);
            });
        },
        fetchPartyOrders(){
            ApiCalls.search(this.URLs.orderMaster,"partyID",this.chalanInData.partyID).then((res)=>{
                this.partyOrders=res.data;
            });
        },
        fetchChalanDetails(){
            ApiCalls.search(this.URLs.chalanInDetails,"chalanId",this.chalanInData.chalanId).then((res)=>{
                this.chalanDetails=res.data;
            });
        },
        fetchOrderDetails(){
            if(!this.orderCheck)
                return;
            ApiCalls.search(this.URLs.orderDetails,"orderNumber",this.chalanInData.orderNo).then((res)=>{
                this.orderDetails=res.data;
                console.log(res.data);
            });
        },
        // updateOrderDetails(id){
        //     return;
        //     let index=this.findCorrespoindingOrderIndex(null,id);
        //     if(index>=0){
        //         ApiCalls.update(this.URLs.orderDetails+"/"+this.orderDetails[index].id,this.orderDetails[index]).then((res)=>{
        //             console.log(res.data);
        //         });
        //     }
        // },
        async updateChalanDetail(chalan,date){
            console.log(date);
            chalan.chalanInDate=date;
            await ApiCalls.update(this.URLs.chalanInDetails+"/"+chalan.id,chalan).then((res)=>{
               console.log(res.data);
            //    if(chalan.orderID !== null)
            //         this.updateOrderDetails(chalan.orderID);
            });
        },
        updateChalanMaster(chalan){
            ApiCalls.update(this.URLs.chalanInMaster+"/"+chalan.id,chalan).then((res)=>{
               let fyData=this.$store.state.financialYearData;
               let index=fyData["chalan_in_masters"].map(data=>data.id).indexOf(res.data.id);
               if(index >= 0)
                fyData["chalan_in_masters"].splice(index,1,res.data);
               this.$store.dispatch("updateChalanInMaster",fyData);
            });
        },
        async deleteChalanDetail(deleteChalan){
            await ApiCalls.delete(this.URLs.chalanInDetails+"/"+deleteChalan.id).then((res)=>{
                console.log(res.data);
                // if(deleteChalan.correspondingOrder>=0){
                //     this.orderDetails[deleteChalan.correspondingOrder]=this.updateOrderQuantity(this.orderDetails[deleteChalan.correspondingOrder],0,deleteChalan.delQty);
                //     this.updateOrderDetails(this.orderDetails[deleteChalan.correspondingOrder].id);
                // }
            });
        },
        deleteChalanEntry(){
            this.chalanDetails.forEach(element=>{
                if(element.id !== null){
                    let index=this.findCorrespoindingOrderIndex(element.desc1);
                    this.deleteChalan.push({"id":element.id,"correspondingOrder":index,"delQty":element.qty});
                    console.log(this.deleteChalan);
                }
            });
            let promises=[];
            if(this.deleteChalan.length>=0){                
                this.deleteChalan.forEach(element=>{promises.push(this.deleteChalanDetail(element));
                console.log(element)});
            }  
            ApiCalls.delete(this.URLs.chalanInMaster+"/"+this.chalanInData.id).then((res)=>{
                console.log(res.data);
                let fyData=this.$store.state.financialYearData;
                let indexMaster=fyData["chalan_in_masters"].map(data=>data.id).indexOf(res.data.id);
                if(indexMaster >= 0)
                    fyData["chalan_in_masters"].splice(indexMaster,1);
                this.$store.dispatch("updateChalanInMaster",fyData);
                Promise.all(promises).then(()=>{
                    this.alertDialogue.success(this.chalanInData.chalanId,3);
                    this.resetAll();
                }).catch((error)=>{
                    this.alertDialogue.error(error,3);
                });                           
            });
        }
    },
    watch:{
        'chalanInData.partyID':function(newValue){
            if(newValue == null)
                return;
            this.fetchPartyOrders();
        },
        'chalanInData.orderNo':function(newValue){
            if(newValue !== "VERBAL" && !this.editing){
                let index=this.partyOrders.map(data=>data.orderNumber).indexOf(newValue);
                this.chalanInData.orderDate=this.partyOrders[index].orderDate;
            }
            this.fetchOrderDetails();
        },
        'chalanInDetailData.desc1':function(newValue){
            if(this.orderCheck){
                let orderIndex=this.findCorrespoindingOrderIndex(newValue,null);
                if(orderIndex>=0){
                    if(this.chalanInDetailData.id===null)
                        this.chalanInDetailData=ChalanInDetailData.assignOrderValue(this.orderDetails[orderIndex]);
                    else 
                        this.chalanInDetailData.orderID=this.orderDetails[orderIndex].id;
                }
            }
        }
    }
}
</script>