let update={
    computed:{
        currentCompany:function(){
            return this.$store.state.companyName;
        },
        checkFinYR:function(){
            let newFinYR=this.$store.state.year;
            return newFinYR.split('-').join('');
        },
        partyMaster:function(){
            return this.$store.state.partyMaster.map(data=>data.id);
        }, 
        chalanInMaster:function(){
            return this.$store.state.financialYearData['chalan_in_masters'].map(data=>data.id);
        },
        chalanOutMaster:function(){
            return this.$store.state.financialYearData['chalan_out_masters'].map(data=>data.id);
        },
        billMaster:function(){
            return this.$store.state.financialYearData['bill_masters'].map(data=>data.id);
        }
    },
    methods:{
        newUpdate(data){
            let action=data.action;
            let dataName='';
            let index=-1;
            if(data.company != this.currentCompany){
                return;
            }
            switch(data.type){
                case "CHALANIN":
                    dataName="chalan_in_masters";
                    index=this.chalanInMaster.indexOf(Number(data.data.id));
                    break;
                case "CHALANOUT":
                    dataName="chalan_out_masters";
                    index=this.chalanOutMaster.indexOf(Number(data.data.id));
                    break;
                case "BILL":
                    dataName="bill_masters";
                    index=this.billMaster.indexOf(Number(data.data.id));
                    break;
                case "PARTY":
                    index=this.partyMaster.indexOf(Number(data.data.id));
                    break;

            }
            if(data.type != "PARTY")
                this.updateData(action,dataName,index,data.data);
            else
                this.updateParty(action,index,data.data);
            
        },
        updateParty(action,index,data){
            let pm=this.$store.state.partyMaster;
            let changes=false;
            switch(action){
                case 'create':
                    if(index<0){
                        pm.push(data);
                        changes=true;
                        console.log('here');
                    }
                    break;
                case 'update':
                    if(index >= 0){
                        pm.splice(index,1,data);
                        changes=true;
                    }
                    break;
                case 'delete':
                    if(index >= 0){
                        pm.splice(index,1);
                        changes=true;
                    }
                    break;
            }
            if(changes)
                this.$store.dispatch("pushParty",pm);
        },
        updateData(action,dataName,index,data){
            let fyData=this.$store.state.financialYearData;
            let changes=false;
            switch(action){
                case 'create':
                    if(index<0 && data.finYR==this.checkFinYR){
                        console.log(this.checkFinYR);
                        fyData[dataName].push(data);
                        changes=true;
                        switch(dataName){
                            case "chalan_in_masters":
                                fyData.chalanInSerial+=1;
                                break;
                            case "chalan_out_masters":
                                fyData.chalanOutSerial+=1;
                                break;
                            case "bill_masters":
                                fyData.billSerial+=1;
                                break; 
                        }
                        console.log('here');
                    }
                    break;
                case 'update':
                    if(index >= 0){
                        fyData[dataName].splice(index,1,data);
                        changes=true;
                    }
                    break;
                case 'delete':
                    if(index >= 0){
                        fyData[dataName].splice(index,1);
                        changes=true;
                    }
                    break;
            }
            if(changes){
                this.$store.dispatch("updateChalanInMaster",fyData);
            }
        }
    }
}
export default update