<template>
    <div  class="modal fade" role="dialog">
        <div class="modal-dialog modal-lg modal-dialog-centered">
            <div class="modal-content">
            <div class="modal-header">
                <h4 class="modal-title">Material Movement Report</h4>
            </div>
            <div class="modal-body">
                    <div class="row">
                        <div class="col-md-12">
                            <div class="row">
                                <label for="fromDate" class="col-form-label text-center col-md-2">From Date:</label>
                                <div class="col-md-4">
                                    <input id="fromDate" type="date" class="form-control" v-model="fromDate">
                                </div>
                                
                    
                                <label for="toDate" class="col-form-label text-center col-md-2">To Date:</label>
                                <div class="col-md-4">
                                    <input id="toDate" type="date" class="form-control" v-model="toDate">
                                </div>
                            </div>  
                        </div>                                  
                    </div>    

                    <div class="row" style="margin-top:10px;">
                        <div class="col-md-12">
                            <div class="row">
                                <label for="partyName" class="col-form-label text-center col-md-2">Party Name:</label>
                                <partydropdown :PartyMaster="PartyMaster" :partyID.sync="partyID"></partydropdown>
                                <label for="orderNumber" class="col-form-label text-center col-md-2">Order Number:</label>
                                <div class="col-md-4">
                                    <select id="orderNo" class="form-control" v-model="orderNumber" @change="selectOrderNumber">
                                        <option v-for="order in partyOrders" :key="order.orderNumber" :value="order.orderNumber">{{order.orderNumber}}</option>
                                    </select>
                                </div>
                            </div>   
                        </div>
                    </div>   

                     <div class="row" style="margin-top:10px;">
                        <div class="col-md-12">
                            <div class="row">
                                <label for="orderNumber" class="col-form-label text-center col-md-2">Description:</label>
                                <div class="col-md-5">
                                    <select id="orderNo" class="form-control" @change="selectedOrderDetails" v-model="desc1">
                                        <option v-for="order in orderDetails" :key="order.desc1" :value="order.desc1">{{order.desc1}}</option>
                                    </select>
                                </div>
                                <div class="col-md-5" >
                                    <input type="text" class="form-control" v-model="desc2" disabled>
                                </div>
                            </div>   
                        </div>
                    </div>      
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-success" @click="showMovementReport" :disabled="validator">Show</button>
                <button type="button" class="btn btn-danger" data-dismiss="modal">Close</button>
            </div>
            </div>

        </div>
    </div>
</template>
<script>
import partydropdown from '../CommonComponents/DropDownParty'
import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'
import $ from 'jquery';
export default {
    name:'materialmodal',
    components:{
        partydropdown,
    },
    data(){
        return {
            partyID:null,
            partyOrders:[],
            orderNumber:null,
            orderDetails:[],
            orderDetail:null,
            fromDate:null,
            toDate:null,
            desc1:null,
        }
    },
    computed:{
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        },
        URLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpoints.ORDERMASTER),
                    orderDetails:URLBuilder.buildURL(URLBuilder.endpoints.ORDERDETAILS),
                    chalanDetails:URLBuilder.buildURL(URLBuilder.endpoints.CHALANOUTDETAILS),
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpoints.CHALANINMASTER),
                    chalanInDetails:URLBuilder.buildURL(URLBuilder.endpoints.CHALANINDETAILS),
                }
            else
                return {
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERMASTER),
                    orderDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERDETAILS),
                    chalanDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANOUTDETAILS),
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANINMASTER),
                    chalanInDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANINDETAILS),
                }
        },
        validator:function(){
            if(this.partyID != null && this.orderDetail != null && this.fromDate != null && this.toDate != null)
                return false;
            else
                return true;
        },
        desc2 : function(){
            if(this.orderDetail != null)
                return this.orderDetail.desc2;
            else
                return '';
        }
    },
    mounted(){
         $('#materialModal').on('hidden.bs.modal',()=>{
            this.resetAll();
         });
    },
    methods:{
        resetAll(){
            this.partyID=null;
            this.partyOrders=[];
            this.orderNumber=null;
            this.orderDetails=[];
            this.orderDetail=null;
            this.fromDate=null;
            this.toDate=null;
            this.desc1=null;
        },
        fetchPartyOrders(){
            ApiCalls.search(this.URLs.orderMaster,"partyID",this.partyID).then((res)=>{
                this.partyOrders=res.data;
                this.orderDetail=null;
                this.desc1='';
            }).catch(error=>{
                console.log(error);
            })
        },
        fetchOrderNumber(orderNumber){
            ApiCalls.search(this.URLs.orderDetails,"orderNumber",orderNumber).then((res)=>{
                this.orderDetails=res.data;
                this.desc1=null;
            }).catch(error=>{
                console.log(error);
            })
        },
        selectOrderNumber(event){
            this.fetchOrderNumber(event.target.value);
        },
        selectedOrderDetails(event){
            let index=this.orderDetails.map(data=>data.desc1).indexOf(event.target.value);
            this.orderDetail=this.orderDetails[index];
        },
        async showMovementReport(){
            let newData=null;
            let notOutData=[];
            await ApiCalls.multiSearch(this.URLs.chalanDetails,[
                {
                    searchContent:'chalanOutDate_gte',
                    searchString:this.fromDate,
                },
                {
                    searchContent:'chalanOutDate_lte',
                    searchString:this.toDate,
                },
                {
                    searchContent:'orderNo',
                    searchString:this.orderDetail.orderNumber,
                },
                {
                    searchContent:'desc1',
                    searchString:this.orderDetail.desc1,
                },
                {
                    searchContent:'_limit',
                    searchString:10000,
                }
            ]).then(res=>{
                newData=res.data;
            });
            await ApiCalls.multiSearch(this.URLs.chalanInMaster,[
                {
                    searchContent:'orderNo',
                    searchString:this.orderDetail.orderNumber,
                },
                {
                    searchContent:'_limit',
                    searchString:10000,
                }
                ]).then(async (res)=>{
                let mappedRes=res.data.map(data=>data.chalanId);
                console.log(mappedRes);
                let chalanInMapped=new Set();
                for(const data of newData){
                    let index=mappedRes.indexOf(data.chalanInId);
                    if(index >= 0){
                        data.chalanInMaster=res.data[index];
                        if(chalanInMapped.has(data.chalanInMaster.chalanId) == false)
                            chalanInMapped.add(data.chalanInMaster.chalanId);
                        await ApiCalls.multiSearch(this.URLs.chalanInDetails,[
                            {
                                searchContent:'desc1',
                                searchString:this.orderDetail.desc1,
                            },
                            {
                                searchContent:'chalanId',
                                searchString:data.chalanInId,
                            }
                        ]).then(chalan=>{
                            data.chalanInDetail=chalan.data[0]
                        });
                    }
                }
                for(const data of res.data){
                    if(chalanInMapped.has(data.chalanId) == false){
                        if(data.chalanDate >= this.fromDate && data.chalanDate <=this.toDate){
                            await ApiCalls.multiSearch(this.URLs.chalanInDetails,[
                            {
                                searchContent:'desc1',
                                searchString:this.orderDetail.desc1,
                            },
                            {
                                searchContent:'chalanId',
                                searchString:data.chalanId,
                            }
                            ]).then(chalan=>{
                                if(chalan.data.length>0)
                                    notOutData.push(
                                        {
                                            'master' : data,
                                            'detail' : chalan.data[0],
                                        }
                                    );
                            });
                        }
                    }
                }
            });
            console.log(notOutData);
            let partyName=this.PartyMaster[this.PartyMaster.map(pm=>pm.id).indexOf(this.partyID)].partyName;
            let newObj={'name':partyName,'order':this.orderDetail,'data':newData,'notOut':notOutData};
            newObj['company']=this.$store.state.companyName;
            newObj['file']=3;
            window.localStorage.setItem('printData',JSON.stringify(newObj));
            this.$emit('print',{
                company:this.$store.state.companyName,
                page:'MaterialMovement'
            });
        }
    },
    watch:{
        partyID:function(newValue){
            if(newValue != null)
                this.fetchPartyOrders();
        }
    }
}
</script>