<template>
    <div class="col-md-1">
        <select id="unitCode" class="form-control" @change="emitChange" v-model="unitType">
            <option  v-for="unit in Units" :key="unit.ROW.UNIT" :value="unit.ROW.UNIT">
                {{unit.ROW.UNIT}}
            </option>
        </select>
    </div>
</template>
<script>
export default {
    name:'unitsdropdown',
    props:{
        unitType:{
            required:true,
        },
        Units:{
            required:true,
        }
    },
    methods:{
        emitChange(){
            this.$emit("update:unitType",this.unitType);
        }
    }
}
</script>