import axios from 'axios'

class ApiCalls{
    get(url){
        url=this.addToken(url);
        return axios.get(url);
    }
    post(url,data){
        url=this.addToken(url);
        return axios.post(url,data);
    }
    update(url,data){
        url=this.addToken(url);
        return axios.put(url,data);
    }
    delete(url){
        url=this.addToken(url);
        return axios.delete(url);
    }
    search(url,searchContent,searchString){
        if(typeof searchString =='string'){
            while(searchString.indexOf('/') !== -1)
                searchString=searchString.replace('/','%2F');
            while(searchString.indexOf('&') !== -1)
                searchString=searchString.replace('&','%26');
        }
        url=url+"?"+searchContent+"="+searchString;
        url=this.addToken(url);
        return axios.get(url);
    }
    searchChalanOutId(url,searchString){
        if(typeof searchString =='string'){
            while(searchString.indexOf('/') !== -1)
                searchString=searchString.replace('/','%2F');
            while(searchString.indexOf('&') !== -1)
                searchString=searchString.replace('&','%26');
        }
        url=url+searchString;
        url=this.addToken(url);
        return axios.get(url);
    }
    multiSearch(url,searchObject){
        let searchUrl=url+"?";
        searchObject.forEach(element => {
            let searchString=element.searchString;
            if(typeof(searchString) == "string"){
                while(searchString.indexOf('/') !== -1)
                    searchString=searchString.replace('/','%2F');
                while(searchString.indexOf('&') !== -1)
                    searchString=searchString.replace('&','%26');
            }
            let lastIndex=searchUrl.length-1;
            searchUrl=searchUrl[lastIndex]==='?'?searchUrl:searchUrl+'&';
            searchUrl=searchUrl+element.searchContent+"="+searchString;
        });
        searchUrl=this.addToken(searchUrl)
        return axios.get(searchUrl);
    }
    addToken(url){
        let token=window.sessionStorage.getItem('user');
        if(token != null){
            token=JSON.parse(token).Token.token;
            if(url.indexOf('?') >= 0)
                url+='&token='+token;
            else
                url+='?token='+token;
        }
        return url;
    }
}

export default new ApiCalls()