<template>
    <div class="container">
        <div class="form-group row no-gutters" style="margin-top:10px;">
            <div class="col-md-2"></div>
            <label for="PartyName" class="col-md-2 col-form-label text-center">Party Name:</label>
            <partydropdown :PartyMaster="PartyMaster" :partyID.sync="partyID"></partydropdown>
        </div>

        <div class="form-group row no-gutters">
            <div class="col-md-2"></div>
            <label for="orderNo" class="col-md-2 col-form-label text-center">Order No.:</label>
            <div class="col-md-2">
                <select id="orderNo" class="form-control" v-model="orderNumber" @change="selectedOrder">
                    <option value="VERBAL">Verbal</option>
                    <option v-for="order in partyOrders" :key="order.orderNumber" :value="order.orderNumber">{{order.orderNumber}}</option>
                </select>
            </div>
            <div class="col-md-1"></div>
            <div class="col-md-2">
                <input id="datePicker" type="date" class="form-control" v-model="orderDate">
            </div>
        </div>

        <div class="row form-group no-gutters">
            <div class="col-md-2"></div>
            <label for="itemDescription" class="col-md-2 col-form-label text-center">Item Description:</label>
            <div class="col-md-6">
                <select class="form-control" @change="selectedChalanOutDetails" v-model="desc1">
                    <option v-for="orderDesc in orderDetails" :key="orderDesc.desc1" :value="orderDesc.desc1">{{orderDesc.desc1}}</option>
                </select>
            </div>
        </div>

        <div class="row form-group no-gutters">
            <div class="col-md-4"></div>
            <div class="col-md-6">
                <input type="text" class="form-control" v-model="desc2" disabled>
            </div>
        </div>

        <hr>

        <rejtable :orderDetails="chalanOutDetails" @changedorder="rejectionAdjustment"></rejtable>

        <hr>

        <div class="row">
            <div class="col-md-12 text-center">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="updateButton">Update</button>
                    <button class="mx-2 btn btn-danger" @click="resetButton">Clear</button>
                    <button class="mx-2 btn btn-danger" @click="navigateTo('/')" >Back</button>
                </div>
            </div>
        </div>

    </div>

</template>
<script>
import navigation from '@/mixins/navigation.js'
import partydropdown from '../CommonComponents/DropDownParty'
import rejtable from './RejectionTable'

import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'
export default {
    name:'RejectionAdjustment',
    mixins:[navigation],
    beforeDestroy(){
        this.resetAll();
    },
    data(){
        return{
            partyOrders:[],
            orderDetails:[],
            chalanOutDetails:[],
            updateChalanOut:[],
            partyID:null,
            orderDate:null,
            orderNumber:null,
            desc1:null,
            desc2:null,
        }
    },
    components:{
        partydropdown,rejtable
    },
    computed:{
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        },
        URLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpoints.ORDERMASTER),
                    orderDetails:URLBuilder.buildURL(URLBuilder.endpoints.ORDERDETAILS),
                    chalanDetails:URLBuilder.buildURL(URLBuilder.endpoints.CHALANOUTDETAILS),
                }
            else
                return {
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERMASTER),
                    orderDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERDETAILS),
                    chalanDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANOUTDETAILS),
                }
        },
        finYR:function(){
            let newFinYR=this.$store.state.year;
            return newFinYR.split('-').join('');
        }
    },
    methods:{
        selectedOrder(event){
            let number=event.target.value;
            this.orderDate=this.partyOrders[this.partyOrders.map(data=>data.orderNumber).indexOf(number)].orderDate;
            this.fetchOrderDetails(number);
        },
        selectedChalanOutDetails(event){
            let selectedOrder=event.target.value;            
            this.desc2=this.orderDetails[this.orderDetails.map(data=>data.desc1).indexOf(selectedOrder)].desc2;
            this.fetchChalanOutDetails();
        },
        rejectionAdjustment(order){
            let index=this.updateChalanOut.map(data=>data.chalanOutID).indexOf(order.chalanOutID);
            if(index>=0)
                this.updateChalanOut.splice(index,1,order);
            else
                this.updateChalanOut.push(order);
            console.log(this.updateChalanOut);
        },
        updateButton(){
            this.updateChalanOut.forEach(element => {
                this.updateChalanOutDetails(element);
            });
            this.resetAll();
        },
        resetButton(){
            this.resetAll();
        },
        resetAll(){
            this.partyOrders=[];
            this.orderDetails=[];
            this.chalanOutDetails=[];
            this.updateChalanOut=[];
            this.partyID=null;
            this.orderDate=null;
            this.orderNumber=null;
            this.desc1=null;
            this.desc2=null;
        },
        fetchPartyOrders(){
            ApiCalls.search(this.URLs.orderMaster,"partyID",this.partyID).then((res)=>{
                this.partyOrders=res.data;
            });
        },
        fetchOrderDetails(number){
            ApiCalls.search(this.URLs.orderDetails,"orderNumber",number).then((res)=>{
                this.orderDetails=res.data;
            });
        },
        fetchChalanOutDetails(){
            ApiCalls.multiSearch(this.URLs.chalanDetails,[
                {'searchContent':'finYR','searchString':this.finYR},
                {'searchContent':'orderNo','searchString':this.orderNumber},
                {'searchContent':'desc1','searchString':this.desc1}
            ]).then((res)=>{
                this.chalanOutDetails=res.data;
            })
        },
        updateChalanOutDetails(chalan){
            ApiCalls.update(this.URLs.chalanDetails+'/'+chalan.id,chalan).then((res)=>{
                console.log(res.data);
            });
        }
        
    },
    watch:{
        partyID:function(newValue){
            if(newValue != null)
                this.fetchPartyOrders();
        }
    }
}
</script>