<template>
    <div  class="modal fade" role="dialog">
        <div class="modal-dialog">
            <div class="modal-content">
            <div class="modal-header">
                <h4 class="modal-title">Previous Orders</h4>
            </div>
            <div class="modal-body">
                <div v-for="order in partyorders" :key="order.orderNumber" class="card" style="width: 50%;" @click="selectedOrder(order)" data-dismiss="modal">
                    <div class="card-body">
                        <h5 class="card-title">{{order.orderNumber}}</h5>
                        <p class="card-text">Date: {{properDate(order.orderDate)}}</p>
                    </div>
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
            </div>
            </div>

        </div>
    </div>
</template>
<script>
export default {
    name:'modal',
    props:{
        partyorders:{
            type:Object,
            required:true
        }
    },
    methods:{
        selectedOrder(order){
            this.$emit('selectedorder',order);
        },
        properDate(date){
            return new Date(date).toLocaleDateString('en-GB');
        }
    }
}
</script>
<style>
.card{
    cursor: pointer;
    float: left;
}
</style>