class OrderDetails{
    constructor(){
        this.orderDetail={
            id:null,
            orderSerial:0,
            desc1:"",
            desc2:"",
            itemCode:"",
            unit:"",
            platingID:0,
            qty:0,
            chalQty:0,
            orderRate:0
        };
        this.orderDetailCopy={...this.orderDetail};
    }
    assignValues(order){
        this.orderDetail={...order};
        return this.orderDetail;
    }
    reset(){
        this.orderDetail={...this.orderDetailCopy};
        return this.orderDetail;
    }
    validator(info){
        if(info.desc1.length==0)
            throw new Error('Description');
        if(info.unit.length==0)
            throw new Error('Unit');
        if(info.platingID==0)
            throw new Error('Plating');
    }
}
export default new OrderDetails();