class ChalanOutDetailData{
    constructor(){
        this.chalanOutDetailData={
            id:null,
            chalanOutID:'',
            chalanOutSl:0,
            chalanOutDate:0,
            chalanInId:null,
            chalanInSl:0,
            chalanInDate:'',//
            partyID:null,//
            desc1:'',
            desc2:'',
            itemCode:'',
            orderNo:null,
            orderRate:null,
            qty:0,
            unit:'',
            rate:0,
            amount:0,
            rejQty:0,
            billQty:0,
            billTag:0,
            finYR:0,
            platingType:'',
        }
        this.copyData={...this.chalanOutDetailData};
    }
    assignValues(data){
        this.chalanOutDetailData={...data};
        return this.chalanOutDetailData;
    }
    assignValuesFromChalanIn(chalan,platings){
        let outQty=chalan.outQty==null?0:chalan.outQty;
        this.id=null;
        this.chalanOutDetailData.chalanInId=chalan.chalanId;
        this.chalanOutDetailData.chalanInSl=chalan.chalanSlNo;
        this.chalanOutDetailData.chalanInDate=chalan.chalanInDate;
        this.chalanOutDetailData.orderRate=this.chalanOutDetailData.rate=chalan.ordRate;
        this.chalanOutDetailData.unit=chalan.unit;
        this.chalanOutDetailData.qty=chalan.qty-outQty;
        this.chalanOutDetailData.desc2=chalan.desc2;
        this.chalanOutDetailData.itemCode=chalan.itemCode;
        this.calculateAmount();
        this.chalanOutDetailData.platingType=platings[platings.map((plating)=>plating.ROW.PLATINGID).indexOf(chalan.platingId)].ROW.PLATINGTYPE;
        return this.chalanOutDetailData;
    }
    calculateAmount(){
        this.chalanOutDetailData.amount=this.chalanOutDetailData.rate*this.chalanOutDetailData.qty;
    }
    resetValue(keep=false){
        let tempDate=this.chalanOutDetailData.chalanInDate;
        let tempID=this.chalanOutDetailData.chalanInId;
        let orderNo=this.chalanOutDetailData.orderNo;
        this.chalanOutDetailData={...this.copyData};
        if(keep){
            this.chalanOutDetailData.chalanInDate=tempDate;
            this.chalanOutDetailData.chalanInId=tempID;
            this.chalanOutDetailData.orderNo=orderNo;
        }
        return this.chalanOutDetailData;
    }
    validator(info){
        if(info.desc1.length==0)
            throw new Error('Description');
        if(info.platingType.length==0)
            throw new Error('Plating Type');
        if(info.unit.length==0)
            throw new Error('Unit');
    }
}

export default new ChalanOutDetailData();