<template>
    <div class="col-md-4">
        <select id="PartyName" @change="emitChange" class="form-control" v-model="partyID">
            <option  v-for="pm in PartyMaster" :key="pm.id" :value="pm.id">
                {{pm.partyName}}
            </option>
        </select>
    </div>
</template>
<script>
export default {
    name:'partydropdown',
    props:{
        PartyMaster:{
            type:Array,
            required:true,
        },
        partyID:{
            required:true,
        }
    },
    methods:{
        emitChange(){
            this.$emit("update:partyID",this.partyID);
        }
    }
}
</script>