<template>
    <div class="col-md-6">
        <select id="platingType" class="form-control" @change="emitChange" v-model="platingID" >
            <option  v-for="plating in Platings" :key="plating.ROW.PLATINGID" :value="plating.ROW.PLATINGID">
                {{plating.ROW.PLATINGTYPE}}
            </option>
        </select>
    </div>
</template>
<script>
export default {
    name:'platingtype',
    props:{
        platingID:{
            required:true,
        },
        Platings:{
            required:true,
        }
    },
    methods:{
        emitChange(){
            this.$emit("update:platingID",this.platingID);
        }
    }
}
</script>