import ApiCalls from '../utils/api';
import URLBuilder from '../utils/URLBuild'

export default{
    changeCompany({commit},newC){
        commit('changeCompany',newC);
    },
    fetchPartyMaster({commit},company){ 
        let url=''; 
        if(company == 0)    
            url=URLBuilder.buildURL(URLBuilder.endpoints.PARTYMASTER);
        else
            url=URLBuilder.buildURL(URLBuilder.endpointsFA.PARTYMASTER);
        url+="?_limit=10000";
        ApiCalls.get(url).then((res)=>{
            commit("addPartyMaster",res.data);
        });
    },
    pushParty({commit},data){
        commit("addPartyMaster",data);
    },
    fetchUnits({commit}){
        let unitURL=URLBuilder.getUnitURL();
        ApiCalls.get(unitURL).then((res)=>{
            commit("addUnits",res.data);
        });
    },
    fetchPlatingID({commit}){
        let platingURL=URLBuilder.getPlatingURL();
        ApiCalls.get(platingURL).then((res)=>{
            commit("addPlating",res.data);
        });
    },
    fetchFinancialYear({commit},finYR){
        console.log(finYR);
        let financialURL='';
        if(finYR.company == 0)
            financialURL=URLBuilder.buildURL(URLBuilder.endpoints.FINANCIALYEAR);
        else
            financialURL=URLBuilder.buildURL(URLBuilder.endpointsFA.FINANCIALYEAR);
        financialURL+="?year="+finYR.year;
        ApiCalls.get(financialURL).then((res)=>{
            if(res.data.length == 0){
                let payload={"year":finYR.year};
                ApiCalls.post(financialURL,payload).then((res)=>{
                    commit("addFinYR",res.data);
                });
            }
            else
                commit("addFinYR",res.data[0]);
        });
    },
    storeFY({commit},finYR){
        commit("addYear",finYR);
    },
    updateChalanInMaster({commit},data){
        commit('addFinYR',data);
    }
};