<template>
    <div class="row">
            <div class="col-md-12 mx-auto custom-scroll table-wrapper">
                <table class="table table-striped table-bordered table-sm" cellspacing="0" width="100%">
                <thead>
                    <tr>
                    <th class="th-sm">Sl.No
                    </th>
                    <th class="th-sm">Plating
                    </th>
                    <th class="th-sm">Description
                    </th>
                    <th class="th-sm">Code
                    </th>
                    <th class="th-sm">Quantity
                    </th>
                    <th class="th-sm">Unit
                    </th>
                    <th class="th-sm">
                        Actions
                    </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="order in orderDetails" :key="order.orderSerial">
                        <td>{{order.chalanSlNo}}</td>
                        <td>{{getPlatingName(order.platingId)}}</td>
                        <td>{{order.desc1}}</td>
                        <td>{{order.itemCode==null?"":order.itemCode}}</td>
                        <td>{{order.qty}}</td>
                        <td>{{order.unit}}</td>
                        <editdelete :emitProperty="order" @edit="findBySerial" @delete="deleteValue"></editdelete>
                    </tr>
                </tbody>
                </table>
            </div>
    </div>
</template>
<script>
import editdelete from '../CommonComponents/EditDeleteTable'
export default {
    name:'chalanintable',
    components:{
        editdelete
    },
    props:{
        orderDetails:{
            type:Object,
            required:true,
        },
        platingDetails:{
            required:true
        },
    },
    methods:{
        getPlatingName(pid){
            let name="";
            this.platingDetails.forEach(element => {
                if(element.ROW.PLATINGID==pid){
                    name=element.ROW.PLATINGTYPE;
                }
            });
            return name;
        },
        findBySerial(obj){
            this.$emit('find',obj);
        },
        deleteValue(obj){
            this.$emit('delete',obj.chalanSlNo);
        }
    }
}
</script>
<style scoped>
.custom-scroll{
    position: relative;
    height: 150px;
    overflow:auto;
}
.table-wrapper{
    display: block;
}
</style>