<template>
    <div  class="modal fade" role="dialog">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
            <div class="modal-header">
                <h4 class="modal-title">Government Contractor Bill</h4>
            </div>
            <div class="modal-body">
                    <div class="row">
                        <div class="col-md-12">
                            <div class="row">
                                <label for="billNumber" class="col-form-label text-center col-md-4">Bill Number:</label>
                                <div class="col-md-8">
                                    <select id="bill" class="form-control" v-model="billNo" @change="selectedBill">
                                        <option v-for="bill in allBills" :key="bill.id" :value="bill.billNo">{{bill.billNo}}</option>
                                    </select>
                                </div>
                            </div>  
                        </div>                                  
                    </div>    

                    <div class="row">
                        <div class="col-md-12" style="margin-top:10px;">
                            <div class="row">
                                <label for="bankName" class="col-form-label text-center col-md-4">Bank Name:</label>
                                <div class="col-md-8">
                                    <input type="text" id="bankName" class="form-control" v-model.lazy="bankName">
                                </div>
                            </div>  
                        </div>                                  
                    </div> 

                    <div class="row">
                        <div class="col-md-12" style="margin-top:10px;">
                            <div class="row">
                                <label for="branchName" class="col-form-label text-center col-md-4">Branch Name:</label>
                                <div class="col-md-8">
                                    <input type="text" id="branchName" class="form-control" v-model.lazy="branchName">
                                </div>
                            </div>  
                        </div>                                  
                    </div>
                    
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-success" :disabled='validator' @click="fetchBillDetails">Front Page</button>
                <button type="button" class="btn btn-success" @click="backPrint">Back Page</button>
                <button type="button" class="btn btn-danger" data-dismiss="modal">Close</button>
            </div>
            </div>

        </div>
    </div>
</template>
<script>
import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'
import $ from 'jquery';
export default {
    name:'contractormodal',
    components:{
       
    },
    data(){
        return {
            bankName:'',
            branchName:'',
            billNo:null,
            billDetails:[],
            billMaster:null,
        }   
    },
    mounted(){
        this.bankName=window.localStorage.getItem('bankName');
        this.branchName=window.localStorage.getItem('branchName');
        $('#contractorModal').on('hidden.bs.modal',()=>{
            this.billNo=null;
            this.billDetails=[];
            this.billMaster=null;
        });
    },
    computed:{
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        },
        allBills:function(){
            let bills=this.$store.state.financialYearData['bill_masters'];
            if(bills != null)
                return bills.sort((a,b)=>{
                    return Number(a.billNo.split('/')[0]) - Number(b.billNo.split('/')[0]);
                });
            return [];
        },  
        validator:function(){
            if(this.billNo != null)
                return false;
            return true;
        },
        URLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpoints.ORDERMASTER),
                    billDetails:URLBuilder.buildURL(URLBuilder.endpoints.BILLDETAILS),
                }
            else
                return {
                    orderMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.ORDERMASTER),
                    billDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.BILLDETAILS),
                }
        },
    },
    methods:{
        selectedBill(event){
            let val=event.target.value;
            this.billNo=val;
            this.billMaster=this.allBills[this.allBills.map(e=>e.billNo).indexOf(val)];
        },
        async fetchBillDetails(){
            let order=null;
            let party=null;
            await ApiCalls.searchChalanOutId(this.URLs.billDetails+'/search/',this.billMaster.billNo).then(async(res)=>{
                this.billDetails=res.data;
                await ApiCalls.search(this.URLs.orderMaster,'orderNumber',this.billDetails[0].orderNumber).then((res)=>{
                    order=res.data[0];
                });
                party=this.PartyMaster[this.PartyMaster.map(pm=>pm.id).indexOf(this.billMaster.partyID)];
            });
            let newObj={
                order:order,
                party:party,
                bills:this.billDetails,
                bankName:this.bankName,
                branchName:this.branchName,
                master:this.billMaster
            };
            newObj['company']=this.$store.state.companyName;
            newObj['file']=2;
            window.localStorage.setItem('printData',JSON.stringify(newObj));
            this.$emit('print',{
                company:this.$store.state.companyName,
                page:'GovernmentContractorPrint'
            });
        },
        backPrint(){
            let newObj={};
            newObj['company']=this.$store.state.companyName;
            newObj['file']=4;
            window.localStorage.setItem('printData',JSON.stringify(newObj));
            this.$emit('print',{
                company:this.$store.state.companyName,
                page:'GovernmentContractorBackPrint'
            });
        }
    },
    watch:{
        bankName:function(newValue){
            window.localStorage.setItem('bankName',newValue);
        },
        branchName:function(newValue){
            window.localStorage.setItem('branchName',newValue);
        }
    }
}
</script>