class PartyInformation{
    constructor(){
        this.partyInfos={
            id:null,
            partyName:"",
            partyType:"",
            gstinNo:"",
            addressee:"",
            partyAddress1:"",
            partyAddress2:"",
            partyAddress3:"",
            partyCode:""
        };
        this.partyInfoCopy={...this.partyInfos};
    }  
    reset(){
        this.partyInfos={...this.partyInfoCopy};
        return this.partyInfos;
    }     
    assignValues(party){
        this.partyInfos.partyName=party.partyName;
        this.partyInfos.id=party.id;
        this.partyInfos.partyType=party.partyType;
        this.partyInfos.gstinNo=party.gstinNo;
        this.partyInfos.addressee=party.addressee;
        this.partyInfos.partyAddress1=party.partyAddress1==null?"":party.partyAddress1;
        this.partyInfos.partyAddress2=party.partyAddress2==null?"":party.partyAddress2;
        this.partyInfos.partyAddress3=party.partyAddress3==null?"":party.partyAddress3;
        this.partyInfos.partyCode=party.partyCode;
        return this.partyInfos;
    }
    validator(info){
        if(info.partyName.length==0)
            throw new Error('Party Name');
        if(info.partyType.length==0)
            throw new Error('Party Type');
    }
}


export default new PartyInformation();
