<template>
    <div class="container">
        <div class="row form-group no-gutters" style="margin-top:10px;">
            <label for="challanOutText" class="col-md-2 col-form-label text-center">Challan Out No.:</label>
            <div class="col-md-4" v-if="disableChalan">
                <input type="text" id="challanOutText" class="form-control" :disabled='disableChalan' :value="currentSerial">
            </div>
            <div class="col-md-4" v-else>
                <input type="text" id="challanOutText" class="form-control" v-model="chalanOutMasterData.chalanOutID">
            </div>
            <div class="col-md">
                <button style="margin-left:10px;" type="button" class="btn btn-info" data-toggle="modal" data-target="#myModal">&#8595;</button>
                <button style="margin-left:10px;" type="button" class="btn btn-info" @click="disableChalan=!disableChalan">Insert</button>
            </div>
            <label for="datePicker" class="col-md-1 col-form-label text-center">Date:</label>
            <div class="col-md-3">
                <input id="datePicker" type="date" class="form-control" v-model="chalanOutMasterData.outDate">
            </div>
        </div>

        <chalanmodal id="myModal" :chalanData="PreviousChalanOutData" @selectchalan="selectedChalan"></chalanmodal>

        <div class="form-group row no-gutters">
            <label for="PartyName" class="col-md-2 col-form-label text-center">Party Name:</label>

            <partydropdown :PartyMaster="PartyMaster" :partyID.sync="chalanOutMasterData.partyID"></partydropdown>

             <label for="PartyName" class="col-md-2 col-form-label text-center">Buyer Name:</label>

            <partydropdown :PartyMaster="BuyerMaster" :partyID.sync="chalanOutMasterData.buyerMaster"></partydropdown>
           
        </div>

        <div class="form-group row no-gutters">
             <div class="col-md-2 mx-auto">
                <input type="text" id="packingText" class="form-control" placeholder="Packing..." v-model="chalanOutMasterData.packing">
            </div>

            <div class="col-md-2 mx-auto">
                <input type="text" id="throughText" class="form-control" placeholder="Through..." v-model="chalanOutMasterData.through">
            </div>
            
            <div class="col-md-2 mx-auto">
                <input type="text" id="rrnoText" class="form-control" placeholder="R.R. No..." v-model="chalanOutMasterData.rrNo">
            </div>

            <div class="col-md-2 mx-auto">
                <input type="text" id="cargoText" class="form-control" placeholder="Cargo..." v-model="chalanOutMasterData.cargo">
            </div>

            <div class="col-md-2 mx-auto">
                <input type="text" id="lrnoText" class="form-control" placeholder="L.R. No..." v-model="chalanOutMasterData.lrNo">
            </div>
        </div>

        <hr>

        <chalanouttable :orderDetails="chalanOutDetails" @find="selectedChalanOut" @delete="deleteValue"></chalanouttable>
        
        <hr>

        <div class="row form-group no-gutters">
            <label for="challanInSelector" class="col-md-2 col-form-label text-center">Challan In No.:</label>
            <div class="col-md-2">
                <select id="challanInSelector" class="form-control" @change="fetchChalanInDetails" v-model="chalanOutDetailData.chalanInId">
                    <option v-for="chalan in presentChalanIN" :key="chalan.id" :value="chalan.chalanId">
                        {{chalan.chalanId}}
                    </option>
                </select>
            </div>
            <label for="itemDescription" class="col-md-2 col-form-label text-center">Item Description:</label>
            <div class="col-md-6">
                <input type="text" class="form-control" list="chalanInDetails" v-model="chalanOutDetailData.desc1">
                <datalist id="chalanInDetails">
                    <option v-for="chalan in chalanInDetails" :key="chalan.id">{{chalan.desc1}}</option>
                </datalist>
            </div>
        </div>

        <div class="row form-group no-gutters">
            <label for="challanInDate" class="col-md-2 col-form-label text-center">Challan In Date.:</label>
            <div class="col-md-2">
                <input id="datePicker" type="date" class="form-control" disabled v-model="chalanOutDetailData.chalanInDate">
            </div>

            <div class="col-md-2">
                <input type="text" id="itemCode" class="form-control" placeholder="ItemCode..." v-model="chalanOutDetailData.itemCode">
            </div>

            <div class="col-md-6">
                <input type="text" class="form-control"  v-model="chalanOutDetailData.desc2">
            </div>
        </div>

        <div class="row form-group no-gutters">
            <label for="platingType" class="col-md-2 col-form-label text-center">Plating Type:</label>
            <div class="col-md-6">
                <input type="text" id="platingType" class="form-control" :disabled='!editPlatingType' v-model="chalanOutDetailData.platingType">
            </div>

            <div class="col-md-2 mx-auto">
                <input type="checkbox" class="form-control" v-model="editPlatingType">
            </div>
        </div>

        <div class="row form-group no-gutters">
            <label for="qtyType" class="col-md-1 col-form-label text-center">Qty:</label>
            <div class="col-md-1">
                <input type="number" id="qtyType" class="form-control" v-model="chalanOutDetailData.qty">
            </div>
            
            <label for="unitCode" class="col-md-1 col-form-label text-center">Unit:</label>
            <div class="col-md-1">
                <input type="text" id="unitCode" class="form-control" disabled v-model="chalanOutDetailData.unit">
            </div>

            <label for="rateCode" class="col-md-1 col-form-label text-center">Rate:</label>
            <div class="col-md-1">
                <input type="number" id="rateCode" class="form-control" v-model="chalanOutDetailData.rate">
            </div>

            <label for="amountCode" class="col-md-1 col-form-label text-center">Amount:</label>
            <div class="col-md-1">
                <input type="number" id="amountCode" class="form-control" v-model="chalanOutDetailData.amount">
            </div>

            <div class="text-center col-md-4">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="addToChalanOutList">Add</button>
                    <button class="mx-2 btn btn-danger" @click="resetBottom(true)">Clear</button>
                </div>
            </div>
            
        </div>

        <div class="row form-group no-gutters">
            <label for="cgst" class="col-md-1 col-form-label text-center" >CGST %:</label>
            <div class="col-md-1">
                <input type="number" id="cgst" class="form-control" v-model="chalanOutMasterData.cgstRate">
            </div>
            
            <label for="cgstVal" class="col-md-1 col-form-label text-center">CGST Val:</label>
            <div class="col-md-1">
                <input type="number" id="cgstVal" class="form-control" disabled v-model="chalanOutMasterData.cgstValue">
            </div>

            <label for="sgst" class="col-md-1 col-form-label text-center">SGST %:</label>
            <div class="col-md-1">
                <input type="number" id="sgst" class="form-control" v-model="chalanOutMasterData.sgstRate">
            </div>

            <label for="sgstVal" class="col-md-1 col-form-label text-center">SGST Val:</label>
            <div class="col-md-1">
                <input type="number" id="sgstVal" class="form-control" disabled v-model="chalanOutMasterData.sgstValue">
            </div>

            <label for="totalGST" class="col-md-1 col-form-label text-center">Total GST:</label>
            <div class="col-md-1">
                <input type="number" id="totalGST" class="form-control" disabled v-model="chalanOutMasterData.totalGST">
            </div>

            <label for="billValue" class="col-md-1 col-form-label text-center">Bill Val:</label>
            <div class="col-md-1">
                <input type="number" id="billValue" class="form-control" disabled v-model="chalanOutMasterData.billValue">
            </div>

        </div>

        <div class="row">
            <div class="col-md-12 text-center">
                <div class="btn-group-horizontal">
                    <button class="mx-2 btn btn-danger" @click="newButton">New</button>
                    <button class="mx-2 btn btn-danger" @click="updateButtonDialogueOpen" :disabled="!editing">Update</button>
                    <button class="mx-2 btn btn-danger" @click="deleteButton">Delete</button>
                    <button class="mx-2 btn btn-danger" @click="navigateTo('/')" >Back</button>
                    <button class="mx-2 btn btn-danger" @click="printWindow">Print</button>
                </div>
            </div>
        </div>
        <alertmodal id='alertmodal'  @updateVal="updateButton" @deleteVal="deleteChalanOutMaster" ref="alertmodal"></alertmodal>
    </div>
</template>
<script>
import navigation from '@/mixins/navigation.js'
import partydropdown from '../CommonComponents/DropDownParty'
import chalanouttable from './ChalanOutTable'
import chalanmodal from './ChalanOutModal'
import ChalanOutMasterData from '@/model/ChalanOutMaster.js'
import ChalanOutDetailData from '@/model/ChalanOutDetail.js'
import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'
import alertmodal from '../CommonComponents/Alerts';
import deleteCheck from '@/mixins/deleteCheck.js'

export default {
    name:'ChallanOut',
    components:{
        partydropdown,chalanouttable,chalanmodal,alertmodal
    },
    mixins:[navigation,deleteCheck],
    props:{
        initialData:{
            type:Object,
        }
    },
    data(){
        return{
            editing:false,
            disableChalan:true,
            editPlatingType:false,
            overrideDescDatalist:false,
            presentChalanIN:[],
            chalanInDetails:[],
            chalanOutDetails:[],
            deletedChalanOutDetails:[],
            chalanOutMasterData:ChalanOutMasterData.chalanOutMasterData,
            chalanOutDetailData:ChalanOutDetailData.chalanOutDetailData,
        }
    },
    computed:{
        alertDialogue:function(){
            return this.$refs.alertmodal;
        },
        currentSerial:function(){
            let serial=this.$store.state.financialYearData.chalanOutSerial;
            if(this.editing)
                return this.chalanOutMasterData.chalanOutID;            
            if(!serial)
                serial=0;
            serial+=1;
            return '0'.repeat(4-serial.toString().length)+serial.toString()+"/"+this.$store.state.year;
        },
        PreviousChalanOutData:function(){
            let data = this.$store.state.financialYearData['chalan_out_masters'];
            if(data==null)
                return [];
            if(data.length>0){
                return data.sort((a,b)=>{
                    return new Date(a.chalanDate)-new Date(b.chalanDate);
                });
            }else
                return data;
        },
        PartyMaster:function(){
            return this.$store.state.partyMaster;
        },
        BuyerMaster:function(){
            return this.$store.state.partyMaster;
        },
        Units:function(){
            return this.$store.state.units;
        },
        Platings:function(){
            if(this.$store.state.companyName == 0)
                return this.$store.state.platingID["RECORD"];
            else
                return this.$store.state.platingID["PLATINGMST"];
        },
        URLs:function(){
            if(this.$store.state.companyName==0)
                return {
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpoints.CHALANINMASTER),
                    chalanInDetails:URLBuilder.buildURL(URLBuilder.endpoints.CHALANINDETAILS),
                    chalanOutMaster:URLBuilder.buildURL(URLBuilder.endpoints.CHALANOUTMASTER),
                    chalanOutDetails:URLBuilder.buildURL(URLBuilder.endpoints.CHALANOUTDETAILS),
                    billDetails:URLBuilder.buildURL(URLBuilder.endpoints.BILLDETAILS),
                }
            else
                return {
                    chalanInMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANINMASTER),
                    chalanInDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANINDETAILS),
                    chalanOutMaster:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANOUTMASTER),
                    chalanOutDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.CHALANOUTDETAILS),
                    billDetails:URLBuilder.buildURL(URLBuilder.endpointsFA.BILLDETAILS),
                }
        },
        finYR:function(){
            let newFinYR=this.$store.state.year;
            return newFinYR.split('-').join('');
        }
    },
    mounted(){
        if(this.initialData != null)
            this.selectedChalan(this.initialData);
    },
    beforeDestroy(){
        this.resetAll();
    },
    methods:{
        addToChalanOutList(){
            try{
                ChalanOutDetailData.validator(this.chalanOutDetailData);
            }catch(error){
                this.alertDialogue.error(error,2);
                return;
            }
            if(this.chalanOutDetailData.chalanOutSl==0){
                this.chalanOutDetailData.chalanOutSl=this.chalanOutDetails.length+1;
                this.chalanOutDetails.push({...this.chalanOutDetailData});                
            }else{
                let delIndex=this.chalanOutDetailData.chalanOutSl-1;
                this.chalanOutDetails.splice(delIndex,1,{...this.chalanOutDetailData});
            }
            this.resetBottom(true);
            this.calculateNewBillValue();
        },
        calculateNewBillValue(){
            let sum=0;
            for(let x=0;x<this.chalanOutDetails.length;x++)
                sum+=Number(this.chalanOutDetails[x].amount);
            this.chalanOutMasterData.billValue=sum+this.chalanOutMasterData.totalGST;
            this.calculateTaxes();
        },
        calculateTaxes(){
            this.chalanOutMasterData.billValue-=this.chalanOutMasterData.totalGST
            this.chalanOutMasterData.cgstValue=this.chalanOutMasterData.billValue*Number(this.chalanOutMasterData.cgstRate)/100;
            this.chalanOutMasterData.sgstValue=this.chalanOutMasterData.billValue*Number(this.chalanOutMasterData.sgstRate)/100;
            this.chalanOutMasterData.totalGST=this.chalanOutMasterData.cgstValue+this.chalanOutMasterData.sgstValue;
            this.chalanOutMasterData.billValue+=this.chalanOutMasterData.totalGST;
            this.chalanOutMasterData.billRoundOff=Math.round(this.chalanOutMasterData.billValue);
            console.log( this.chalanOutMasterData.billRoundOff)
        },
        selectedChalan(chalan){
            this.resetBottom();
            this.disableChalan=true;
            this.editing=true;
            this.chalanOutMasterData=ChalanOutMasterData.assignValues(chalan);
            this.currentSerial=this.chalanOutMasterData.chalanOutID;
            this.fetchChalanOutDetails();
            console.log(chalan.chalanOutID);
        },
        deleteValue(id){
            id=id-1;
            if(this.chalanOutDetails[id].id != null){
                this.deletedChalanOutDetails.push(this.chalanOutDetails[id].id);
            }
            this.chalanOutDetails.splice(id,1);
            for(let i=0;i<this.chalanOutDetails.length;i++){
                this.chalanOutDetails[i].chalanOutSl=i+1;
            }
            this.calculateNewBillValue();
        },
        updateButtonDialogueOpen(){
            this.alertDialogue.update(this.chalanOutMasterData.chalanOutID);
        },
        newButton(){
            if(this.chalanOutMasterData.id != null)
                this.resetAll();
            else
                this.createChalanOutMaster();
        },
        updateButton(){
            this.updateChalanOutMaster();
            this.chalanOutDetailsUpdaterCreator();
        },
        async deleteButton(){
            if(this.chalanOutMasterData.id != null){
                try{
                    await this.chalanOutMasterDeleteCheck(this.URLs.billDetails,this.chalanOutMasterData.chalanOutID);
                }catch(error){
                    this.alertDialogue.error(error,1);
                    return;
                }
                this.alertDialogue.delete(this.chalanOutMasterData.chalanOutID);
            }else
                this.resetAll();
        },
        async printWindow(){
            let data={};
            data['chalanOutMasterData']=this.chalanOutMasterData;
            data['chalanOutDetails']=this.chalanOutDetails;
            if(this.chalanOutDetails.length>0){
                data['chalanInMaster']=this.presentChalanIN[
                    this.presentChalanIN.map(ele=>{
                        return ele.chalanId;
                    }).indexOf(this.chalanOutDetails[0].chalanInId)
                ];
            }
            data['partyMaster']=this.PartyMaster[
                this.PartyMaster.map(ele=>{
                    return ele.id;
                }).indexOf(this.chalanOutMasterData.partyID)
            ];
            data['buyerMaster']=this.chalanOutMasterData.buyerMaster ? this.PartyMaster[
                this.PartyMaster.map(ele=>{
                    return ele.id;
                }).indexOf(this.chalanOutMasterData.buyerMaster)
            ] : data['partyMaster'];
            data['company']=this.$store.state.companyName;
            data['file']=1;
            //Poorly Handly empty CHalan Masteer
            if(data.chalanInMaster == null){
                await ApiCalls.search(this.URLs.chalanInMaster,'chalanId',data.chalanOutDetails[0].chalanInId).then(res=>{
                    data.chalanInMaster=res.data[0];
                });
            }
            window.localStorage.setItem('printData',JSON.stringify(data));
            this.$emit('print',{
                company:this.$store.state.companyName,
                page:'ChallanOutPrint'
            });
            //window.open('Print/ChallanOutPrint.html');
            console.log(data);
        },
        selectedChalanOut(chalan){
            this.overrideDescDatalist=true;
            this.chalanOutDetailData=ChalanOutDetailData.assignValues(chalan);
        },
        searchChalanInDesc(desc1){
            return this.chalanInDetails[this.chalanInDetails.map((val)=>val.desc1).indexOf(desc1)];
        },
        calculateAmount(){
            this.chalanOutDetailData.amount=Number(this.chalanOutDetailData.rate*this.chalanOutDetailData.qty);
        },
        resetBottom(keep=false){
            this.chalanOutDetailData=ChalanOutDetailData.resetValue(keep);
        },
        resetTop(){
            this.chalanOutMasterData=ChalanOutMasterData.resetValue();
        },
        resetAll(){
            this.editing=false;
            this.presentChalanIN=[];
            this.chalanOutDetails=[];
            this.deletedChalanOutDetails=[];
            this.resetTop();
            this.resetBottom();
        },
        updateFY(data,operation=0){
            // let fyData=this.$store.state.financialYearData;
            // let indexMaster=fyData["chalan_out_masters"].map(ele=>ele.id).indexOf(data.id);
            // switch(operation){
            //     case 0:
            //         //New
            //         fyData.chalanOutSerial=fyData.chalanOutSerial==null?0+1:fyData.chalanOutSerial+1;
            //         fyData['chalan_out_masters'].push(data);
            //         break;
            //     case 1:
            //         //Update
            //         fyData["chalan_out_masters"].splice(indexMaster,1,data);
            //         break;
            //     case 2:
            //         fyData["chalan_out_masters"].splice(indexMaster,1);
            //         break;
            // }            
            // this.$store.dispatch("updateChalanInMaster",fyData);
            console.log(operation);
        },
        createChalanOutDetailObj(chalan){
            chalan.chalanOutID=this.chalanOutMasterData.chalanOutID;
            chalan.chalanOutDate=this.chalanOutMasterData.outDate;
            chalan.partyID=this.chalanOutMasterData.partyID;
            chalan.finYR=this.finYR;
            return chalan;
        },
        fetchChalanInDetails(event){
            let chalanInId=event.target.value;
            let index=this.presentChalanIN.map((data)=>data.chalanId).indexOf(chalanInId);
            this.chalanOutDetailData.chalanInDate=this.presentChalanIN[index].chalanDate;
            this.chalanOutDetailData.orderNo=this.presentChalanIN[index].orderNo;
            ApiCalls.search(this.URLs.chalanInDetails,"chalanId",chalanInId).then((res)=>{
                this.chalanInDetails=res.data;
            });
        },
        fetchChalanOutDetails(){
            this.chalanOutDetails=[];
            ApiCalls.searchChalanOutId(this.URLs.chalanOutDetails+'/search/',this.chalanOutMasterData.chalanOutID).then((res)=>{
                res.data.forEach(data=>{
                    let newData={...data.chalanOutData};
                    if(data.chalanInData != null){
                        newData.itemCode=data.chalanInData.itemCode;
                        newData.desc2=data.chalanInData.desc2;
                    }
                    this.chalanOutDetails.push(newData);
                });
            });
        },
        chalanOutDetailsUpdaterCreator(){
            let promises=[];
            if(this.deletedChalanOutDetails.length>0){
                this.deletedChalanOutDetails.forEach((ele)=>{
                    promises.push(this.deleteChalanOutDetail(ele));
                })
            }
            this.chalanOutDetails.forEach(chalan => {
                let updatedChalan=this.createChalanOutDetailObj(chalan);
                if(updatedChalan.id != null)
                    promises.push(this.updateChalanOutDetail(updatedChalan));
                else
                    promises.push(this.createChalanOutDetails(updatedChalan));
            });
            Promise.all(promises).then(()=>{
                if(this.chalanOutMasterData.id==null)
                    this.alertDialogue.success(this.chalanOutMasterData.chalanOutID,1);
                else
                    this.alertDialogue.success(this.chalanOutMasterData.chalanOutID,2);
                this.resetAll();
            }).catch(error=>{
                this.alertDialogue.error(error,3);
                return;
            });
        },
        createChalanOutMaster(){
            this.chalanOutMasterData.chalanOutID=this.disableChalan?this.currentSerial:this.chalanOutMasterData.chalanOutID;
            this.chalanOutMasterData.finYR=this.finYR;
            try{
                ChalanOutMasterData.validator(this.chalanOutMasterData)
            }catch(error){
                this.alertDialogue.error(error,2);
                return;
            }
            ApiCalls.post(this.URLs.chalanOutMaster,this.chalanOutMasterData).then((res)=>{
                console.log(res);
                //this.updateFY(res.data);
                this.chalanOutDetailsUpdaterCreator();
            }).catch(error=>{
                this.alertDialogue.error(error,3);
                return;
            });
        },
        async createChalanOutDetails(chalan){
            await ApiCalls.post(this.URLs.chalanOutDetails,chalan).then((res)=>{
                console.log(res.data);
            });
        },
        async updateChalanOutDetail(chalan){
            await ApiCalls.update(this.URLs.chalanOutDetails+'/'+chalan.id,chalan).then((res)=>{
                console.log(res.data);
            });
        },
        updateChalanOutMaster(){
            ApiCalls.update(this.URLs.chalanOutMaster+'/'+this.chalanOutMasterData.id,this.chalanOutMasterData).then((res)=>{
                console.log(res.data);
                //this.updateFY(res.data,1);
            });
        },
        async deleteChalanOutDetail(id){
            await ApiCalls.delete(this.URLs.chalanOutDetails+"/"+id).then((res)=>{
                console.log(res.data);
            });
        },
        async deleteChalanOutMaster(){
            await ApiCalls.delete(this.URLs.chalanOutMaster+"/"+this.chalanOutMasterData.id).then((res)=>{
                console.log(res.data);
                //this.updateFY(res.data,2);
                let promises=[];
                this.chalanOutDetails.forEach((ele)=>{
                    promises.push(this.deleteChalanOutDetail(ele.id));
                });
                if(this.deletedChalanOutDetails.length>0){
                    this.deletedChalanOutDetails.forEach((ele)=>{
                        promises.push(this.deleteChalanOutDetail(ele));
                    });
                }
                Promise.all(promises).then(()=>{
                    this.alertDialogue.success(this.chalanOutMasterData.chalanOutID,3);
                    this.resetAll();
                }).catch(error=>{
                    this.alertDialogue.error(error,3);
                });
            });
        }
    },
    watch:{
        "chalanOutMasterData.partyID":async function(newValue){
            if(newValue === null)
                return;
            let prevYear=this.$store.state.year.split('-');
            for(let x=0;x<prevYear.length;x++){
                prevYear[x]-=1;
            }
            prevYear=prevYear.join('');
            console.log(prevYear);
            let prevData=[];
            await ApiCalls.multiSearch(this.URLs.chalanInMaster,[
                {
                    searchContent:"partyID",
                    searchString:newValue,
                },
                {
                    searchContent:"finYR",
                    searchString:prevYear,
                },
                {
                    searchContent:"cTag",
                    searchString:0,
                },
            ]).then(res=>{
                prevData=res.data;
            })
            this.presentChalanIN=this.$store.state.financialYearData['chalan_in_masters'].filter((chalan)=>{
                if(this.editing)
                    return chalan.partyID==newValue;
                else
                    return (chalan.partyID==newValue && (chalan.cTag == 0 || chalan.cTag == null));
            }).sort((a,b)=>{
                return Number(a.chalanId.split('/')[0])-Number(b.chalanId.split('/')[0]);
            })
            this.presentChalanIN=prevData.concat(this.presentChalanIN);
        },
        "chalanOutMasterData.cgstRate":function(){
            this.calculateTaxes();
        },
        "chalanOutMasterData.sgstRate":function(){
            this.calculateTaxes();
        },
        "chalanOutDetailData.desc1":function(newValue){
            if(this.overrideDescDatalist){
                this.overrideDescDatalist=false;
                return;
            }
            if(newValue.length>0){
                let chalan=this.searchChalanInDesc(newValue);
                if(chalan != null){
                    this.chalanOutDetailData=ChalanOutDetailData.assignValuesFromChalanIn(chalan,this.Platings);
                    console.log(this.chalanOutDetailData);
                }
            }
        },
        "chalanOutDetailData.rate":function(){
            this.calculateAmount();
        },
        "chalanOutDetailData.qty":function(){
            this.calculateAmount();
        },        
    }
}
</script>