<template>
    <div  class="modal fade" role="dialog">
        <div class="modal-dialog">
            <div class="modal-content">
            <div class="modal-header">
                <h4 class="modal-title">Chalan-Out Entries</h4>
            </div>
            <div class="modal-body">                
                <div class="card" style="width: 100%;">
                    <ul class="list-group list-group-flush">
                        <li class="list-group-item" v-for="chalan in chalanData" :key="chalan.chalanOutID"  @click="selectedOrder(chalan)">
                            {{chalan.chalanOutID}}
                            <span class="mx-auto"></span>
                            {{properDate(chalan.outDate)}}
                        </li>
                    </ul>
                </div>               
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
            </div>
            </div>

        </div>
    </div>
</template>
<script>
export default {
    name:'entrymodal',
    props:{
        chalanData:{
            type:Object,
            required:true
        }
    },
    methods:{
        selectedOrder(data){
            this.$emit('selectchalan',data);
        },
        properDate(date){
            return new Date(date).toLocaleDateString('en-GB');
        }
    }
}
</script>
<style>
.card{
    cursor: pointer;
    float: left;
}
</style>