<template>
    <div class='container'>
        <div class="row form-group no-gutters" style="margin-top:20%;">
            <div class="col-md-3"></div>
            <label for="username" class="col-md-2 col-form-label text-center">User Name:</label>
            <div class="col-md-4">
                <input type="text" id="username" class="form-control">
            </div>
            <div class="col-md-3"></div>
        </div>

        <div class="row form-group no-gutters" style="margin-top:10px;">
            <div class="col-md-3"></div>
            <label for="password" class="col-md-2 col-form-label text-center">Password:</label>
                <div class="col-md-4">
                    <input type="password" id="password" class="form-control">
                </div>
            <div class="col-md-3"></div>
        </div>
        <div class="row">
            <div class="col-md-8"></div>
            <div class="col-md">
                <button type="button" class="btn btn-success" @click="login">Login</button>
            </div>
            <div class="col-md-2"></div>
        </div>
    </div>
</template>

<script>
import URLBuilder from '@/utils/URLBuild.js'
import ApiCalls from '@/utils/api.js'
export default{
    name:'LoginPage',
    computed:{
        
    },
    methods:{
        async login(){
            let user=document.getElementById('username').value;
            let password=document.getElementById('password').value;
            await ApiCalls.post(URLBuilder.url+'auth/local',{
                identifier:user,
                password:password,
            }).then((res)=>{
                console.log(res);
                window.sessionStorage.setItem('user',JSON.stringify(res.data.user));
                this.$router.push('/');
            }).catch((error)=>{
                console.log(error);
            });
        }
    }
}
</script>
